import Report from "../js/report";

class CounterAttackBlock{
    constructor(applicator,tactics,type,damageValue,rate,duration,waitTime){
        //来自谁
        this.applicator = applicator;
        //施加该效果的战法
        this.tactics = tactics;
        //类型
        this.type = type;
        //伤害率
        this.damageValue = damageValue;
        //触发率
        this.rate = rate;
        //持续时间
        this.duration = duration;
        //等待时间
        this.waitTime = waitTime;
        this.beExecuted = null;
    }
    /**
     * 
     * @param {*} target 
     * @param {*} needReport 
     * @param {*} reportLevel 
     * @param {*} reportStyle 1:A武将获得x能力 2:A武将【x】令B武将获得x能力 3:A武将【x】施加的的x效果消失了 4:A武将【x】施加的x效果被移除了 5:A武将【x】移除了B武将x效果
     * @returns 
     */
    Init(target,needReport = true,reportLevel = 3,reportStyle = 1){
        this.beExecuted = target;
        //冲突检测
        for(let i in target.SPEC_ATTACK){
            if(target.SPEC_ATTACK[i].type == this.type){
                let r = new Report();
                r.InitExecuteReport(3,target,`已存在${this.type}效果`);
                return false;
            }
        }
        target.SPEC_ATTACK.push(this);
        if(needReport){
            this.InitReport(reportStyle,reportLevel)
        }
        return true;
    }
    /**
     * 生成战报
     * @param {*} reportStyle 1:A武将获得x能力 2:A武将【x】令B武将获得x能力 3:A武将【x】施加的的x效果消失了 4:A武将【x】施加的x效果被移除了 5:A武将【x】移除了B武将x效果
     * @param {*} reportLevel 
     * @param {*} newApplicator 
     * @param {*} newTact 
     */
    InitReport(reportStyle=1,reportLevel=3,newApplicator = null, newTact = null){
        let r = new Report();
        let msg = `获得${this.type}能力`
        if(reportStyle == 1) r.InitExecuteReport(reportLevel,this.beExecuted,msg);
        if(reportStyle == 2) r.InitTwoTargetReport(reportLevel,this.applicator,this.beExecuted,`【${this.tactics.name}】令`,msg);
        if(reportStyle == 3) {
            let r = new Report("状态信息");
            r.InitExecuteReport(reportLevel,this.beExecuted,`【${this.tactics.name}】施加的${this.type}效果消失了`);
        }
        if(reportStyle == 4) r.InitExecuteReport(reportLevel,this.beExecuted,`【${this.tactics.name}】施加的${this.type}效果被移除了`);
        if(reportStyle == 4 && newApplicator && newTact) r.InitTwoTargetReport(reportLevel,newApplicator,this.beExecuted,`【${newTact.name}】移除了`,`${this.type}效果`);
    }
    SelfDestroy(){
        for(let i in this.beExecuted.SPEC_ATTACK){
            if(this.beExecuted.SPEC_ATTACK[i] == this){
                this.beExecuted.SPEC_ATTACK.splice(i,1);
            }
        }
    }
}

export default CounterAttackBlock;