import Report from "../js/report";

const dmgTypeMsg = {
    "谋略": "策略攻击伤害",
    "攻击": "攻击伤害",
    "主动": "主动战法伤害",
    "追击": "追击战法伤害",
    "指挥": "指挥战法伤害",
    "被动": "被动战法伤害",
    "普通攻击": "普通攻击伤害",
    "所有": "所有伤害",
}


export class DamageVarBlock {
    /**
     * 伤害增减益块
     * @param {object} applicator 该块的施加者
     * @param {object} tactics 施加该块的战法
     * @param {string} damageType 处理的伤害类型，攻击or谋略
     * @param {boolean} isMakeDamage 是否为造成伤害的增减益，反之为受到伤害的增减益
     * @param {number} value 数值，-表示减伤，+表示增加
     * @param {boolean} isOnceAttack 是否作用在一次计算伤害中
     * @param {boolean} isOnceTactics 是否作用在一整次战法的计算中（可收益多段伤害）
     * @param {number} duration 该块的持续时间
     * @param {number} waitTime 该块作用前的等待时间
     */
    constructor(applicator, tactics, damageType, isMakeDamage, value, isOnceAttack, isOnceTactics, duration, waitTime,isBenefit) {
        this.applicator = applicator;
        this.tactics = tactics;
        this.damageType = damageType;
        this.isMakeDamage = isMakeDamage
        this.value = value;
        this.isOnceAttack = isOnceAttack;
        this.isOnceTactics = isOnceTactics == undefined ? false : isOnceTactics;
        this.duration = duration;
        this.waitTime = waitTime == undefined ? 0 : waitTime;
        this.workToTact = null;
        this.isBenefit = isBenefit;
        if(isBenefit == undefined){
            if (this.isMakeDamage) {
                if (this.value > 0) this.isBenefit = true;
            }
            else {
                if (this.value < 0) this.isBenefit = true;
            }
        }
        this.beExecuted = null;
    }
    /**
     * 将块放入目标身上
     * @param {object} target 该块需要被初始化在哪个武将上
     * @param {object} workToTact 表明对制定战法的增减伤，不需要说明特地为某战法增伤不需要传入tacticsItem，该参数用于袁绍等武将标记
     * @param {boolean} needReport 是否需要生成战报
     * @param {number} reportLevel 战报等级
     * @param {number} reportStyle 战报风格：1.A .... 2.A【x】令B .... 
     * @returns 
     */
    Init(target, workToTact = null, needReport = true, reportLevel = 3, reportStyle = 1) {
        this.workToTact = workToTact;
        this.beExecuted = target
        //所有类型的增减伤块会进行降级
        if (this.damageType == '所有') {
            let ad = false; let tact = false;
            for (let i in target.DAMAGE_VAR) {
                let block = target.DAMAGE_VAR[i];
                if (block.isMakeDamage == this.isMakeDamage
                    && block.isBenefit == this.isBenefit
                    && block.tactics.type == this.tactics.type) {
                    if (block.damageType == '攻击') ad = true;
                    if (block.damageType == '谋略') tact = true;
                }
            }
            if (ad && tact) {
                let msg = "已存在"
                if (!this.isMakeDamage) msg += "受到"
                msg += "所有伤害";
                if (this.value >= 666) msg += "大幅提高";
                else if (this.value >= 0 && this.value < 666) msg += '提高';
                else if (this.value < -666) msg += "大幅降低"
                else msg += '降低';
                msg += '效果';
                let report = new Report();
                report.InitExecuteReport(3, target, msg);
                return false;
            }
            //退化
            else if (ad) this.damageType = '谋略';
            else if (tact) this.damageType = '攻击';
        }
        //冲突判断
        for (let i in target.DAMAGE_VAR) {
            let block = target.DAMAGE_VAR[i];
            //同一个战法的一次性增伤不会产生冲突
            let onceControl = true;
            if (block.isOnceAttack && block.tactics == this.tactics) onceControl = false;
            else if (block.isOnceAttack != this.isOnceAttack && block.tactics == this.tactics) onceControl = false;
            //说明新的增伤效果和旧的作用于同一个战法，解决袁绍的战法冲突问题
            if (this.workToTact != null && this.workToTact == block.workToTact
                && this.isBenefit == block.isBenefit
                && this.damageType == block.damageType
                && block.tactics.type == this.tactics.type) {
                let msg = `已存在【${this.workToTact.name}】造成${this.damageType}伤害`
                if (this.value >= 666 || this.value < -666) msg += "大幅"
                msg += this.isBenefit ? "提高效果" : "降低效果";
                let report = new Report();
                report.InitExecuteReport(3, target, msg);
                return false;
            }
            //作用类型一样
            if (this.workToTact == null
                && block.damageType == this.damageType
                && block.isMakeDamage == this.isMakeDamage
                && block.tactics.type == this.tactics.type
                && block.isBenefit == this.isBenefit
                && onceControl) {
                let msg = "已存在";
                if (!this.isMakeDamage) msg += "受到"
                msg += dmgTypeMsg[this.damageType];
                if (this.value >= 666 || this.value < -666) msg += "大幅";
                if (this.isMakeDamage) msg += this.isBenefit ? "提高" : "降低";
                else msg += !this.isBenefit ? "提高" : "降低";
                let report = new Report();
                report.InitExecuteReport(3, target, msg);
                return false;
            }
        }
        target.DAMAGE_VAR.push(this);
        if (needReport) {
            this.InitReport(reportStyle, reportLevel);
        }
        return true;
    }
    /**
     * 生成战报
     * @param {*} reportStyle 1类: A武将x伤害提高x% 2类: A武将【x】令B武将x伤害提高x% 3类：A武将【x】的x伤害提高x%效果消失了 4类:A武将来自【x】的x伤害提高x%效果被移除了 5类:A武将【x】移除了B武将x伤害提高x%
     * @param {*} reportLevel 
     * @param {*} isByInit 
     * @param {*} newApplicator 
     * @param {*} newTact 
     */
    InitReport(reportStyle = 1, reportLevel = 3, newApplicator = null, newTact = null) {
        if (this.workToTact == null) {
            let msg = "";
            if (!this.isMakeDamage) msg += "受到";
            msg += dmgTypeMsg[this.damageType];
            if (this.value >= 666 || this.value < -666) msg += "大幅";
            if (this.isMakeDamage) msg += this.isBenefit ? "提高" : "降低";
            else msg += !this.isBenefit ? "提高" : "降低";

            if (reportStyle == 1 || reportStyle == 2) {
                if (this.value >= -666 && this.value < 666 && this.value != 0) msg += `${Math.abs(this.value % 1 != 0 ? Number(this.value.toFixed(1)) : Math.floor(this.value))}%`;
                if (this.beExecuted.driver.turn < 1 && this.value == 0) msg += '效果已施加'
            }
            else if (reportStyle == 3 || reportStyle == 4 || reportStyle == 5) {
                if (this.value >= -666 && this.value < 666 && this.value != 0) msg += `${Math.abs(this.value % 1 != 0 ? Number(this.value.toFixed(1)) : Math.floor(this.value))}%`;
            }
            let r = new Report();
            if (reportStyle == 1) r.InitExecuteReport(reportLevel, this.beExecuted, msg);
            if (reportStyle == 2) r.InitTwoTargetReport(reportLevel, this.applicator, this.beExecuted, `【${this.tactics.name}】令`, msg);
            if (reportStyle == 3){
                let r = new Report("状态信息");
                r.InitExecuteReport(reportLevel, this.beExecuted, `【${this.tactics.name}】施加的` + msg + "效果消失了");
            }
            if (reportStyle == 4) r.InitExecuteReport(reportLevel, this.beExecuted, `【${this.tactics.name}】施加的` + msg + "效果被移除了");
            if (reportStyle == 5) r.InitTwoTargetReport(reportLevel, newApplicator, this.beExecuted, `【${newTact.name}】移除了`, msg + "效果");
        }
        else {
            let msg = `【${this.workToTact.name}】造成`
            if (this.damageType == '谋略') msg += "策略攻击伤害";
            if (this.damageType == '攻击') msg += "攻击伤害";
            if (this.value >= 666 || this.value < -666) msg += "大幅";
            if (this.isMakeDamage) msg += this.isBenefit ? "提高" : "降低";
            else msg += !this.isBenefit ? "提高" : "降低";
            if (this.value >= -666 && this.value < 666 && this.value != 0) msg += `${Math.abs(this.value % 1 != 0 ? Number(this.value.toFixed(1)) : Math.floor(this.value))}%`;
            if (reportStyle < 3) {
                if (this.beExecuted.driver.turn < 1 && this.value == 0) msg += '效果已施加'
            }
            let r = new Report();
            if (reportStyle == 1) r.InitExecuteReport(reportLevel, this.beExecuted, msg);
            if (reportStyle == 2) r.InitTwoTargetReport(reportLevel, this.applicator,this.beExecuted,`【${this.tactics.name}】令`, msg);
            if (reportStyle == 3) {
                r = new Report("状态信息");
                r.InitExecuteReport(reportLevel, this.beExecuted, msg + "效果消失了");
            }
            if (reportStyle == 4) r.InitExecuteReport(reportLevel, this.beExecuted, msg + "效果被移除了");
        }
    }
}

export class CureVarBlock{
    /**
     * 恢复效果变化
     * @param {*} applicator 
     * @param {*} tactics 
     * @param {*} value 
     * @param {*} isOutput true：输出恢复效果增减  false：受到恢复效果增减
     * @param {*} isOnceCure 
     * @param {*} duration 
     * @param {*} waitTime 
     * @param {object|null} workTactics 具体指定战法
     */
    constructor(applicator,tactics,value,isOutput,isOnceCure,duration,waitTime,workTactics){
        this.applicator = applicator;
        this.tactics = tactics;
        this.value = value;
        this.isOutput = isOutput;
        this.isOnceCure = isOnceCure;
        this.duration = duration;
        this.waitTime = waitTime == undefined ? 0 : waitTime;
        this.workTactics = workTactics || null;
        this.beExecuted = null;
    }
    Init(target,needReport = false,reportStyle = 1,reportLevel = 3){
        this.beExecuted = target;
        for(let i in target.CURE_VAR){
            if(target.CURE_VAR[i].tactics.type == this.tactics.type
            && target.CURE_VAR[i].isOutput == this.isOutput
            ){
                let report = new Report();
                report.InitExecuteReport(3,target,`已存在${target.CURE_VAR[i].isOutput?"造成":"受到"}恢复${this.value>0?"提高":"降低"}效果`);
                return false;
            }
        }
        target.CURE_VAR.push(this);
        if(needReport){
            this.InitReport(reportStyle,reportLevel)
        }
        return true;
    }
    /**
     * 生成战报
     * @param {*} reportStyle 
     * @param {*} reportLevel 
     * @param {*} newApplicator 
     * @param {*} newTact 
     */
    InitReport(reportStyle = 1, reportLevel = 3, newApplicator = null, newTact = null){
        let r = new Report();
        let msg = "";
        if(this.workTactics) msg += `【${this.workTactics.name}】`
        msg += this.isOutput ? "造成恢复效果" : "受到恢复效果"
        msg += this.value > 0 ? "提高" : "降低";
        if(this.value == 0 && this.applicator.driver.turn < 1) msg += "效果已施加";
        else msg += `${Math.abs(this.value)}%`

        if (reportStyle == 1) r.InitExecuteReport(reportLevel, this.beExecuted, msg);
        if (reportStyle == 2) r.InitTwoTargetReport(reportLevel, this.applicator, this.beExecuted, `【${this.tactics.name}】令`, msg);
        if (reportStyle == 3){
            let r = new Report("状态信息");
            r.InitExecuteReport(reportLevel, this.beExecuted, `【${this.tactics.name}】施加的` + msg + "效果消失了");
        }
        if (reportStyle == 4) r.InitExecuteReport(reportLevel, this.beExecuted, `【${this.tactics.name}】施加的` + msg + "效果被移除了");
        if (reportStyle == 5) r.InitTwoTargetReport(reportLevel, newApplicator, this.beExecuted, `【${newTact.name}】移除了`, msg + "效果");
    }
}

export class DamageStatusVarBlock {
    /**
     * 特殊状态伤害增减块
     * @param {*} applicator 
     * @param {*} tactics 
     * @param {*} damageType 伤害基础类型：攻击，谋略
     * @param {*} statusType 伤害类型：燃烧，妖术，恐慌。。。
     * @param {*} isMakeDamage 是否是造成伤害的一方
     * @param {*} value 增减伤数值
     * @param {*} isOnceAttack 是否只作用一次
     * @param {*} duration 持续时间
     * @param {*} waitTime 等待时间
     */
    constructor(applicator, tactics, damageType, statusType, isMakeDamage, value, isOnceAttack, duration, waitTime, workTactics) {
        this.applicator = applicator;
        this.tactics = tactics;
        this.damageType = damageType;
        this.statusType = statusType
        this.isMakeDamage = isMakeDamage
        this.value = value;
        this.isOnceAttack = isOnceAttack;
        this.duration = duration;
        this.waitTime = waitTime == undefined ? 0 : waitTime;
        this.workTactics = workTactics || null;
        this.isBenefit = false;
        if (this.isMakeDamage && this.value > 0) this.isBenefit = true;
        else if (!this.isMakeDamage && this.value < 0) this.isBenefit = true;

        this.beExecuted = null;
    }
    Init(target, isNeedReport = false, reportLevel = 3, reportStyle = 1) {
        this.beExecuted = target;
        for (let i in target.STATUS_DAMAGE_VAR) {
            if (target.STATUS_DAMAGE_VAR[i].tactics.type == this.tactics.type
                && this.statusType == target.STATUS_DAMAGE_VAR[i].statusType
                && this.isMakeDamage == target.STATUS_DAMAGE_VAR[i].isMakeDamage
            ) {
                let r = new Report();
                r.InitExecuteReport(3, target, `已存在${this.isMakeDamage ? "造成" : "受到"}${this.statusType}伤害${this.value > 0 ? "提高" : "降低"}效果`);
                return false;
            }
        }
        target.STATUS_DAMAGE_VAR.push(this);
        if (isNeedReport) {
            this.InitReport(reportStyle, reportLevel)
        }
        return true;
    }
    /**
     * 生成战报
     * @param {*} reportStyle 1类: A武将x伤害提高x% 2类: A武将【x】令B武将x伤害提高x% 3类：A武将【x】的x伤害提高x%效果消失了 4类:A武将来自【x】的x伤害提高x%效果被移除了 5类:A武将【x】移除了B武将x伤害提高x%
     * @param {*} reportLevel 
     * @param {*} isByInit 
     * @param {*} newApplicator 
     * @param {*} newTact 
     */
    InitReport(reportStyle = 1, reportLevel = 3, newApplicator = null, newTact = null) {
        let r = new Report();
        let msg = `${this.isMakeDamage ? "造成" : "受到"}${this.statusType}伤害${this.value > 0 ? "提高" : "降低"}${Math.abs(this.value)}%`
        if (reportStyle == 1) r.InitExecuteReport(reportLevel, this.beExecuted, msg, true)
        if (reportStyle == 2) r.InitTwoTargetReport(reportLevel, this.applicator, this.beExecuted, `【${this.tactics.name}】令`, msg);
        if (reportStyle == 3) {
            let r = new Report("状态信息");
            r.InitExecuteReport(reportLevel, this.beExecuted, `【${this.tactics.name}】施加的` + msg + "效果消失了");
        }
        if (reportStyle == 4) r.InitExecuteReport(reportLevel, this.beExecuted, `【${this.tactics.name}】施加的` + msg + "效果被移除了");
        if (reportStyle == 5) r.InitTwoTargetReport(reportLevel, newApplicator, this.beExecuted, `【${newTact.name}】移除了`, msg + "效果");
    }
}



export class DamageCureDiscountBlock {
    /**
     * 对最终伤害或者是治疗量*某数(刘禅等需要战法效益减少的武将)
     * @param {*} applicator 
     * @param {*} tactics 
     * @param {*} output 
     * @param {string} isDamageOrCure “伤害”或者“治疗”
     * @param {*} value 
     * @param {*} duration 
     * @param {*} waitTime 
     * @param {*} workTactics 
     * @param {*} isRatio 是否是百分比还是固定值，默认百分比
     */
    constructor(applicator, tactics, output, isDamageOrCure, value, duration, waitTime, workTactics, isRatio) {
        //来自谁
        this.applicator = applicator;
        //施加该效果的战法
        this.tactics = tactics;
        //是输出还是接受
        this.output = output;
        //是治疗还是伤害
        this.isDamageOrCure = isDamageOrCure
        //数值
        this.value = value;
        //持续时间
        this.duration = duration;
        //等待事件
        this.waitTime = waitTime == undefined ? 0 : waitTime;
        //作用于哪个战法 (指定特定战法为其增伤减伤)
        this.workTactics = workTactics == undefined ? null : workTactics;
        //是否是增益
        this.isBenefit = false;
        this.isRatio = isRatio == undefined ? true : isRatio;
        this.beExecuted = null;
    }
    Init(target, needReport = false, reportStyle = 1, reportLevel = 3) {
        this.beExecuted = target;
        if (this.isDamageOrCure != '伤害' && this.isDamageOrCure != '治疗') {
            console.log('this.isDamageOrCure字段出错', this.isDamageOrCure);
            return;
        }
        if (this.value > 0) this.isBenefit = true;
        if (needReport) {
            this.InitReport(reportStyle,reportLevel);
        }
        target.DISCOUNT.push(this);
    }
    /**
     * 生成战报
     * @param {*} reportStyle 
     * @param {*} reportLevel 
     * @param {*} newApplicator 
     * @param {*} newTact 
     */
    InitReport(reportStyle = 1, reportLevel = 3, newApplicator = null, newTact = null){
        let r = new Report();
        let msg = "";
        if(this.workTactics) msg += `【${this.workTactics.name}】`
        msg += this.isDamageOrCure ? "输出伤害" : "输出恢复";
        msg += this.value > 0 ? "提高" : "降低";
        msg += `${Math.abs(this.value)}%`;
        if(reportStyle == 1) r.InitExecuteReport(reportLevel,this.beExecuted,msg);
        if(reportStyle == 2) r.InitTwoTargetReport(reportLevel,this.applicator,this.beExecuted,`【${this.tactics.name}】令`,msg);
        if(reportStyle == 3){
            let r = new Report("状态信息");
            r.InitExecuteReport(reportLevel,this.beExecuted,msg + "效果消失了");
        }
        if(reportStyle == 4) r.InitExecuteReport(reportLevel,this.beExecuted,msg + "效果被移除了");
        if(reportStyle == 5) r.InitTwoTargetReport(reportLevel,newApplicator,this.beExecuted,`【${newTact.name}】移除了`,msg);
    }
    //清除自己
    SelfDestroy() {
        let remain = [];
        this.beExecuted.DISCOUNT.forEach(block => {
            if (block != this) remain.push(block);
        });
        this.beExecuted.DISCOUNT = remain;
    }
}