            let value = GetAdditionValue(self, 17, '攻击', 0.1);
            let realRange = GetRange(self, this);
            let realTgtNum = 2
            let res = GetTarget(self, realRange, true, false, realTgtNum, true, true, this);
            //追加
            let plus = true;
            self.ally.forEach(tgt => {
                let arm = GetArmsTypeChar(tgt.upArms[tgt.choseUpArmsIdx]);
                if (tgt != self && arm != GetArmsTypeChar(self.upArms[self.choseUpArmsIdx])) {
                    plus = false;
                }
            })
            for (let i in res) {
                let cure = CountCure(self, res[i], cureValue, cureIndex, this);
                Cure(self, res[i], cure, this, true, null, 1, 3);
            }
