export const comboS = [
    //乘胜追击
    {
        id: 10,
        name: "乘胜追击",
        type: "追击",
        rate: 30,
        range: 1,
        isDamageMaker: true,
        damageMakeType: ["攻击"],
        isControlMaker: false,
        isCureMaker: false,
        selectFri: false,
        selectSelf: false,
        selectEnemy: true,
        targetNumber: 1,
        arms: ['骑', "步", "弓"],
        wait: 0,
        execute(self, target) {
            let dmgMult = 150;//基础伤害率
            let dmgMult2 = 100;//第二段伤害
            let rate = 60;//触发几率
            let odd_decline = 20;//几率衰减
            //造成伤害
            let [damage, dmgId] = CountDamage(self, target, dmgMult, "攻击", false, this, null);
            Hurt(dmgId, self, target, damage, false, "攻击", this, true);
            let time = 1;
            for (let i = 0; i < time; i++) {
                let realRate = GetMoraleRate(self, rate) - (time - 1) * odd_decline;
                if (realRate <= 0) return;
                let rand = Math.floor(Math.random() * 100);
                if (rand < realRate) {
                    let [damage, dmgId] = CountDamage(self, target, dmgMult2, "攻击", false, this, null);
                    Hurt(dmgId, self, target, damage, false, "攻击", this, true);
                    time += 1;
                }
            }
            this.STATISTICS.times += 1;
        }
    },
]

