import Report from "../js/report";


/**
 * 清除异常或有益效果
 * @param {object} applicator 该次清除操作的发起者
 * @param {object} target 被清除的对象
 * @param {string} tactType 清除由哪一种战法类型施加的block(主动，被动，指挥，追击，所有，所有表示无论是战法施加还是兵种宝物施加的都移除)
 * @param {object} isBenefit 清除有益效果还是清除有害效果
 * @param {object} orgTact 发起该次清除操作的战法
 * @param {boolean} needReport 是否需要输出战法，默认需要
 * @param {number} reportStyle 战报风格（1: 某效果被移除 2: 某战法移除了某效果 ，3: 某效果消失了）
 * @param {number} reportLevel 战报的等级，默认3级
 * @param {object} tgtTact 是否需要清除指定战法施加的状态(不清除其他)，不需要指定就默认null 
 */
export const ClearWhichTypeTacticsInitStatus = (applicator, target, tactType, isBenefit, orgTact, needReport = true, reportStyle = 1, reportLevel = 3, tgtTact = null) => {
    let count = 0;
    //清除状态效果
    count += ClearStatusBlock(applicator, target, tactType, isBenefit, orgTact, needReport, reportStyle, reportLevel);
    //清除属性加成
    count += ClearAttrBlock(applicator, target, tactType, isBenefit, orgTact, needReport, reportStyle, reportLevel);
    //清除伤害加成
    count += ClearDamageVarBlock(applicator, target, tactType, isBenefit, orgTact, needReport, reportStyle, reportLevel);
    //移除状态类型伤害的增减伤
    count += ClearStatusDamageBlock(applicator, target, tactType, isBenefit, orgTact, needReport, reportStyle, reportLevel)
    //移除状态类型伤害(只有有害的)
    if (!isBenefit) count += ClearSpecialStatus(applicator, target, tactType, orgTact, needReport, reportStyle, reportLevel)
    //清除距离加成
    count += ClearRangeBlock(applicator, target, tactType, isBenefit, orgTact, needReport, reportStyle, reportLevel);
    //移除战法发动几率和准备时间的增减益
    count += ClearOddVarBlock(applicator, target, tactType, isBenefit, orgTact, needReport, reportStyle, reportLevel);
    return count;
}


/**
 * 清除状态
 * @param {*} applicator 
 * @param {*} target 
 * @param {*} tactType 
 * @param {*} isBenefit 
 * @param {object} orgTact 
 * @param {*} needReport 
 * @param {number} reportStyle 战报风格：1.来自【x】施加的x状态被移除了，2.x【x】令x的x状态消失了 3.x的x状态消失了
 * @param {*} reportLevel 
 * @param {object} tgtTact 是否需要清除指定战法施加的状态(不清除其他)，不需要指定就默认null 
 * @returns 
 */
export const ClearStatusBlock = (applicator, target, tactType, isBenefit, orgTact, needReport = true, reportStyle = 1, reportLevel = 3, tgtTact = null) => {
    let remain = [];
    let count = 0
    target.STATUS.forEach(block => {
        if (tactType == "所有" && block.isBenefit == isBenefit || (block.isBenefit == isBenefit && block.tactics.type == tactType)) {
            if(tgtTact != null && block.tactics == tgtTact || tgtTact == null){
                if (needReport) {
                    if(reportStyle == 1) block.InitReport(4,reportLevel,applicator,orgTact);
                    if(reportStyle == 2) block.InitReport(5,reportLevel,applicator,orgTact);
                    if(reportStyle == 3) block.InitReport(3,reportLevel,applicator,orgTact);
                }
                count += 1;
            }
            
        }
        else remain.push(block);
    })
    target.STATUS = remain;
    return count;
}

