/**
 * 清除持续伤害类状态 
 * @param {object} applicator 
 * @param {object} target 
 * @param {string} tactType 清除由哪一种战法类型施加的block(主动，被动，指挥，追击，所有，所有表示无论是战法施加还是兵种宝物施加的都移除)
 * @param {object} orgTact 
 * @param {boolean} needReport 
 * @param {number} reportStyle 1：A来自【x】被移除了  2：A【x】令B...效果消失了  3：A来自...效果消失了
 * @param {number} reportLevel 
 * @param {object} tgtTact 是否需要清除指定战法施加的状态(不清除其他)，不需要指定就默认null 
 * @returns 返回清理计数
 */
export const ClearSpecialStatus = (applicator, target, tactType, orgTact, needReport = true, reportStyle = 1, reportLevel = 3, tgtTact = null) => {
    let count = 0;
    let remain = [];
    target.SPECIAL_STATUS.forEach(block => {
        if ((tgtTact != null && tgtTact == block.tactics) || tgtTact == null && (tactType == "所有" || block.tactics.type == tactType)) {
            if (needReport) {
                if(reportStyle == 1) block.InitReport(4,reportLevel,applicator,orgTact);
                if(reportStyle == 2) block.InitReport(5,reportLevel,applicator,orgTact);
                if(reportStyle == 3) block.InitReport(3,reportLevel,applicator,orgTact);
            }
            count++;
        }
        else remain.push(block);
    });
    target.SPECIAL_STATUS = remain;
    return count;
}


/**
 * 清除某个whichArr数组中由某个战法施加的block
 * @param {object} target 
 * @param {string} whichArr 
 * @param {object} tactItem 
 * @returns 返回清理计数
 */
export const ClearBlockByTact = (target, whichArr, tactItem) => {
    let count = 0;
    let remain = [];
    target[whichArr].forEach(block => {
        if (block.tactics != tactItem) remain.push(block);
        else count +=1;
    })
    target[whichArr] = remain;
    return count;
}

/**
 * 清除某个whichArr数组中某个具体已知的block (目前没有可调用场景)
 * @param {object} target 
 * @param {string} whichArr 
 * @param {object} tgtBlock 
 * @returns 返回清理计数
 */
export const ClearTargetBlock = (target, whichArr, tgtBlock) => {
    let count = 0;
    let remain = [];
    target[whichArr].forEach(block => {
        if (block != tgtBlock) remain.push(block);
        else count +=1;
    })
    target[whichArr] = remain;
    return count;
}

