
//陷入持续伤害型状态块时执行
export class FallIntoContinueDamageExecuteBlock{
    constructor(applicator,tactics,execute,duration){
        //来自谁
        this.applicator = applicator;
        //施加该效果的战法
        this.tactics = tactics;
        //执行函数
        this.execute = execute;
        //被执行人
        this.beExecuted = null,
        //持续时间
        this.duration = duration;

        //状态类型(恐慌，妖术...)
        this.type = null;
        //伤害类型
        this.damageType = null;//或者是谋略，默认攻击
        //伤害率
        this.damageValue = 0;
        //施加该伤害的武将
        this.originWarrior = null;
        //该持续伤害的施加战法
        this.originTactics = null;
        //持续时间
        this.typeDuration = 0;
        //该持续型状态
        this.statusDmgBlock = null;
    }
    Init(target){
        this.beExecuted = target;
        target.FALL_INTO_CONTINUE_DAMAGE.push(this);
    }
    GetData(type,damageType,damageValue,originWarrior,originTactics,duration,statusDmgBlock){
        //状态类型(恐慌，妖术...)
        this.type = type;
        //伤害类型
        this.damageType = damageType;//或者是谋略，默认攻击
        //伤害率
        this.damageValue = damageValue;
        //施加该伤害的武将
        this.originWarrior = originWarrior;
        //该持续伤害的施加战法
        this.originTactics = originTactics;
        //持续时间
        this.typeDuration = duration;
        this.statusDmgBlock = statusDmgBlock;
    }
    Execute(){
        this.execute();
    }
}

//陷入特殊状态时执行
export class FallIntoStatusExecuteBlock{
    /**
     * 
     * @param {*} applicator 来自谁
     * @param {*} tactics 施加该效果的战法
     * @param {*} execute 执行函数
     * @param {*} duration 持续时间
     * @param {*} isBefore 是在陷入前执行还是陷入后
     */
    constructor(applicator,tactics,execute,duration,isBefore){
        this.applicator = applicator;
        this.tactics = tactics;
        this.execute = execute;
        this.duration = duration;
        this.isBefore = isBefore == undefined ? false :  isBefore;

        this.beExecuted = null,
        //状态施加者
        this.originWarrior = null;
        //状态施加战法
        this.originTactics = null;
        //状态类型
        this.type = "";
        //状态的持续时间
        this.typeDuration = 0;
        //是否是增益
        this.isBenefit = false;
        //等待时间
        this.typeWaitTime = 0;
        //该状态块的索引
        this.StatusBlock = null;
        
    }
    Init(target){
        this.beExecuted = target;
        if(this.isBefore){
            target.BEFORE_FALL_INTO_STATUS.push(this);
        }
        else target.FALL_INTO_STATUS.push(this);
        return true;
    }
    GetData(originWarrior,originTactics,type,typeDuration,isBenefit,typeWaitTime,StatusBlock){
        //状态施加者
        this.originWarrior = originWarrior;
        //状态施加战法
        this.originTactics = originTactics;
        //状态类型
        this.type = type;
        //持续时间
        this.typeDuration = typeDuration;
        //是否是增益
        this.isBenefit = isBenefit;
        //等待时间
        this.typeWaitTime = typeWaitTime;
        this.StatusBlock = StatusBlock
    }
    Execute(){
        this.execute();
    }
}


