import { IsDodge, IsGetStatus } from "./judger";
import Report from "../js/report";
import { ConsDodgeBlock, ConsOnceDamageBlock, HandlerOnceDamageVarReport } from "./handler";


/**
 * 受到伤害
 * @param {string} dmgId - 伤害的唯一标识，和damage计算绑定
 * @param {Object} attacker - 造成伤害的对象
 * @param {Object} receiver - 受到伤害的对象
 * @param {number} orgDmgVal - 基础伤害值（原始伤害）
 * @param {boolean} isComAtk - 造成该次伤害是否为普通攻击
 * @param {string} damageType - 伤害类型（策略伤害还是攻击伤害）
 * @param {Object|null} tacticsItem - 造成伤害的来源战法（可能为空）
 * @param {boolean} [needReport=true] - 是否需要生成战报
 * @param {Object|null|number} [reportStyle=1] - 战报类型，1：x损失了...，2：x【x】令x损失了...，3：x由于【x】的x效果，损失了... 4：由于同心效果，损失了...，5：为友军分摊伤害...默认1
 * @param {string|null} [statusDmgType=null] - 该伤害为哪一个持续伤害类型，没有则填null
 * @param {number} [reportLevel=3] 战报等级，默认为3
 * @returns {Object} 包含最终伤害和战报的对象,returnMsg = {
        realDmg : damage,
        status : true,
    }
*/
export const Hurt = (dmgId,attacker, receiver, orgDmgVal, isComAtk, damageType, tacticsItem, needReport = true, reportStyle = 1, statusDmgType = null,reportLevel = 3) => {
    let damage = orgDmgVal;
    for(let i in receiver.BEFORE_GET_HURT){
        receiver.BEFORE_GET_HURT[i].Execute(dmgId,attacker, receiver, orgDmgVal,isComAtk,damageType,tacticsItem,statusDmgType);
        damage = receiver.BEFORE_GET_HURT[i].damageValue;
    }
    
    //真正扣血
    if(damage > receiver.hp) damage = receiver.hp;
    receiver.hp -= damage;
    //产生伤兵
    receiver.wound += Math.floor(damage * 0.95);
    if (receiver.hp <= 0) {
        receiver.hp = 0;
        receiver.wound = Math.floor(receiver.wound * 0.6)
        receiver.alive = false;
    }
    //战报填入
    if (needReport) {
        let report = new Report();
        if(reportStyle == 1 || reportStyle == null) report.InitExecuteReport(reportLevel, receiver, `损失了${damage}兵力(${receiver.hp})`);
        if(reportStyle == 2){
            if(tacticsItem) report.InitTwoTargetReport(reportLevel,attacker,receiver,`【${tacticsItem.name}】令`,`损失了${damage}兵力(${receiver.hp})`)
            else report.InitTwoTargetReport(reportLevel,attacker,receiver,`令`,`损失了${damage}兵力(${receiver.hp})`)
        }
        if(reportStyle == 3){
            if(statusDmgType){
                report.InitTwoTargetReport(reportLevel,receiver,attacker,`由于`,`【${tacticsItem.name}】的${statusDmgType}效果，损失了${damage}兵力(${receiver.hp})`)
            }
        }
        if(reportStyle == 4){
            report.InitExecuteReport(reportLevel,receiver,`由于同心效果，损失了${damage}兵力(${receiver.hp})`)
        }
        if(reportStyle == 5){
            report.InitExecuteReport(reportLevel,receiver,`为友军分摊伤害，损失了${damage}兵力(${receiver.hp})`)
        }
    }

    //消耗掉一次性的增减伤加成块战报
    HandlerOnceDamageVarReport(attacker,dmgId);
    HandlerOnceDamageVarReport(receiver,dmgId);

    //统计攻击方输出
    if (isComAtk || tacticsItem == null) attacker.STATISTICS.atk += damage;
    else {
        tacticsItem.STATISTICS.damage += damage;
    }
    //调用攻击方造成伤害后执行函数
    if (attacker.alive && damage != 0) {
        let len = attacker.MAKE_DAMAGE.length;
        for (let i = 0; i < len; i++) {
            if (!attacker.MAKE_DAMAGE[i]['action']) {
                attacker.MAKE_DAMAGE[i].GetDamageData(damage, receiver, tacticsItem, isComAtk, damageType,statusDmgType)
                attacker.MAKE_DAMAGE[i].Execute();
                attacker.MAKE_DAMAGE[i]['action'] = true;
            }
            if (len != attacker.MAKE_DAMAGE.length) {
                i = 0;
                len = attacker.MAKE_DAMAGE.length;
            }
        }
        //清空action
        for (let i in attacker.MAKE_DAMAGE) {
            attacker.MAKE_DAMAGE[i]['action'] = false;
        }
    }
    //调用受到伤害后执行函数
    if (receiver.alive && damage != 0) {
        let length = receiver.AFTER_DAMAGE.length;
        for (let i = 0; i < length; i++) {
            if (isComAtk) {
                if (receiver.AFTER_DAMAGE[i].getAdDmgExecute || receiver.AFTER_DAMAGE[i].getCAttackExecute) {
                    receiver.AFTER_DAMAGE[i].GetDamageData(damage, attacker, tacticsItem, isComAtk, damageType,statusDmgType)
                    receiver.AFTER_DAMAGE[i].Execute();
                }
            }
            else {
                if (tacticsItem.type == '指挥' && receiver.AFTER_DAMAGE[i].getTacticsDmgExecute.command
                    || tacticsItem.type == '主动' && receiver.AFTER_DAMAGE[i].getTacticsDmgExecute.active
                    || tacticsItem.type == '追击' && receiver.AFTER_DAMAGE[i].getTacticsDmgExecute.combo
                    || tacticsItem.type == '被动' && receiver.AFTER_DAMAGE[i].getTacticsDmgExecute.passive) {

                    if (receiver.AFTER_DAMAGE[i].getAdDmgExecute && !receiver.AFTER_DAMAGE[i].getTactDmgExecute) {
                        if (damageType == '攻击') {
                            receiver.AFTER_DAMAGE[i].GetDamageData(damage, attacker, tacticsItem, isComAtk, damageType,statusDmgType)
                            receiver.AFTER_DAMAGE[i].Execute();//执行后receiver.AFTER_DAMAGE可能会变短
                        }
                    }
                    else {
                        receiver.AFTER_DAMAGE[i].GetDamageData(damage, attacker, tacticsItem, isComAtk, damageType,statusDmgType)
                        receiver.AFTER_DAMAGE[i].Execute();//执行后receiver.AFTER_DAMAGE可能会变短
                    }

                }
            }
            if (receiver.AFTER_DAMAGE.length != length) {
                i--;
                length = receiver.AFTER_DAMAGE.length;
            }
        }
    }
    //调用受到持续型伤害后执行的函数，xp周瑜主战法
    if (receiver.alive && damage != 0 && statusDmgType) {
        let length = receiver.AFTER_CONTINUE_DAMAGE.length;
        for (let i = 0; i < length; i++) {
            if (tacticsItem.type == '指挥' && receiver.AFTER_CONTINUE_DAMAGE[i].getTacticsDmgExecute.command
                || tacticsItem.type == '主动' && receiver.AFTER_CONTINUE_DAMAGE[i].getTacticsDmgExecute.active
                || tacticsItem.type == '追击' && receiver.AFTER_CONTINUE_DAMAGE[i].getTacticsDmgExecute.combo
                || tacticsItem.type == '被动' && receiver.AFTER_CONTINUE_DAMAGE[i].getTacticsDmgExecute.passive) {
                receiver.AFTER_CONTINUE_DAMAGE[i].GetData(damage, attacker, tacticsItem, damageType);
                receiver.AFTER_CONTINUE_DAMAGE[i].Execute();//执行后receiver.AFTER_DAMAGE可能会变短
            }
            if (receiver.AFTER_CONTINUE_DAMAGE.length != length) {
                i--;
                length = receiver.AFTER_CONTINUE_DAMAGE.length;
            }
        }
    }
    let returnMsg = {
        realDmg : damage,
        status : true,
    }
    return returnMsg;
}


/**
 * 受到治疗效果
 * @param {Object} initiator - 造成治疗效果的对象
 * @param {Object} receiver - 受到治疗的对象
 * @param {number} cureNumber - 基础治疗数值
 * @param {Object} [cureTactics=null] - 造成这次恢复效果的战法对象
 * @param {boolean} [needReport=true] - 是否需要生成战报
 * @param {Object|null} [reportParma=null] - 自行生成的战报
 * @param {number} [reportStyle=1] - 战报类型，1：x恢复了...，2：x【x】令x恢复了...，3：x由于【x】的x效果，恢复了...，默认1
 * @param {number} [reportLevel=3] - 战报等级
 * @returns {Object} 包含最终伤害和战报的对象
*/
export const Cure = (initiator, receiver, cureNumber, cureTactics = null, needReport = true, reportParma = null, reportStyle = 1, reportLevel = 3,statusType=null) => {
    //确保治疗效果不能是负数
    let cure = cureNumber > 0 ? cureNumber : -cureNumber;
    if (IsGetStatus(receiver, "围困")) {
        let report = new Report();
        report.InitExecuteReport(3, receiver, `处于围困状态，不可回复兵力`);
        return false;
    }

    //真正回血
    if(cure > receiver.wound) cure = receiver.wound;
    receiver.hp += cure;
    receiver.wound -= cure;
    //统计
    cureTactics.STATISTICS.cure += cure;
    if (needReport) {
        if (reportParma == null) {
            let report = new Report();
            if (reportStyle == 1) report.InitExecuteReport(reportLevel, receiver, `恢复了${cure}兵力(${receiver.hp})`);
            if (reportStyle == 2) {
                if (cureTactics) report.InitTwoTargetReport(reportLevel, initiator, receiver, `【${cureTactics.name}】令`, `恢复了${cure}兵力(${receiver.hp})`);
                else report.InitTwoTargetReport(reportLevel, initiator, receiver, `令`, `恢复了${cure}兵力(${receiver.hp})`);
            }
            if(reportStyle == 3){
                if (cureTactics){
                    report.InitTwoTargetReport(reportLevel, initiator,receiver,`【${cureTactics.name}】的${statusType}效果为`,`恢复了${cure}兵力(${receiver.hp})`)
                }
                else report.InitExecuteReport(reportLevel, receiver, `由于${statusType}效果，恢复了${cure}兵力(${receiver.hp})`);
            }

        }
        else if (reportParma != null) {
            let r = new Report();
            r.PutReportToHeap(receiver.global.ReportHeap, reportParma);
        }
    }
    //执行受到恢复后执行函数
    receiver.GET_CURE.forEach(block => {
        block.Execute(cureTactics, initiator, cureNumber);
    });
    return true;
}

