import Report from "../js/report";

class IgnoreBlock{
    /**
     * 无视某一项属性
     * @param {*} applicator 
     * @param {*} tactics 
     * @param {*} ignoreAttrType 无视的属性名称
     * @param {*} value 无视的数值
     * @param {*} duration 块的持续时间
     * @param {*} waitTime 块生效的等待时间
     * @param {*} workTactics 是否作用特殊指定战法（使某个战法无视属性）
     */
    constructor(applicator,tactics,ignoreAttrType,value,duration,waitTime,workTactics){
        this.applicator = applicator;
        this.tactics = tactics;
        this.ignoreAttrType = ignoreAttrType;
        this.value = value;
        this.duration = duration;
        this.waitTime = waitTime;
        this.workTactics = workTactics == undefined ? null : workTactics;
        this.beExecuted = null;
    }
    Init(target,needReport = true,reportLevel = 3,reportStyle = 1){
        this.beExecuted = target;
        //判断冲突
        for(let i in target.IGNORE){
            if(target.IGNORE[i].tactics.type == this.tactics.type && target.IGNORE[i].ignoreAttrType == this.ignoreAttrType){
                let r = new Report();
                r.InitExecuteReport(3,target,`已存在无视${this.ignoreAttrType}属性效果`);
                return false;
            }
        }
        target.IGNORE.push(this);
        if(needReport){
            this.InitReport(reportStyle,reportLevel);
        }
        return true;
    }
    /**
     * 生成战报
     * @param {*} reportStyle 1:A武将无视x%x属性 2:A武将【x】令B武将无视x%的x属性 3:A武将无视x%的x属性效果消失了 4:A武将无视x%的x属性效果被移除了 5:A武将【x】移除了B武将无视x%x属性效果
     * @param {*} reportLevel 
     * @param {*} newApplicator 
     * @param {*} newTact 
     */
    InitReport(reportStyle = 1, reportLevel = 3, newApplicator = null, newTact = null){
        let r = new Report();
        let msg = `无视${this.value}%${this.ignoreAttrType}属性`;
        if(this.beExecuted.driver.turn < 1) msg += "效果已施加";

        if(reportStyle == 1) r.InitExecuteReport(reportLevel,this.beExecuted,msg);
        if(reportStyle == 2) r.InitTwoTargetReport(reportLevel,this.applicator,this.beExecuted,`【${this.tactics.name}】令`,msg);
        if(reportStyle == 3) {
            let r = new Report("状态信息");
            r.InitExecuteReport(reportLevel,this.beExecuted,msg+"效果消失了");
        }
        if(reportStyle == 4) r.InitExecuteReport(reportLevel,this.beExecuted,msg+"效果被移除了");
        if(reportStyle == 5 && newApplicator && newTact) r.InitTwoTargetReport(reportLevel,newApplicator,this.beExecuted,`【${newTact.name}】移除了`,msg+"效果");
    }
}
export default IgnoreBlock;