

export const isObjectInArray = (arr, targetObj)=> {
    return arr.some(item=> item === targetObj);
}

export const pushObjectNoRepeat = (arr,targetObj)=>{
    if(!isObjectInArray(arr,targetObj)) arr.push(targetObj);
}

/**
 * 格式化数字
 * @param {*} num 
 * @param {*} fixed 
 * @returns 
 */
export const formatNumber = (num, fixed = 0)=> {
    // 处理非数字情况
    if (typeof num !== 'number' || isNaN(num)) return num;
    const multiplier = Math.pow(10, fixed);
    // 四舍五入处理
    const rounded = Math.round(num * multiplier) / multiplier;

    if (fixed === 0) return rounded;
    const numStr = rounded.toString();
    if (!numStr.includes('.')) {
        // 整数情况：直接返回
        return rounded;
    }

    const [integerPart, decimalPart] = numStr.split('.');
    const truncatedDecimal = decimalPart.slice(0, fixed);
    return Number(`${integerPart}.${truncatedDecimal}`);
}