import Report from "../js/report";
import { GetMoraleRate } from "./get";


export const ConsBlockDuration = (target)=>{
    ConsStatusBlock(target);
    ConsAttrBlock(target);
    //伤害治疗变化
    ConsDamageVarBlock(target);
    ConsDamageStatusVarBlock(target);
    ConsCureVarBlock(target);
    //其余
    ConsResistBlock(target);
    ConsRateAndSkip(target);
    ConsRangBlock(target);
    ConsIgnoreBlock(target);
    ConsCounterAttackBlock(target);
    //执行类
    ConsDamageStatusBlock(target);
    ConsCureStatusBlock(target);

    ConsAttrAppliedBlock(target);
    ConsFallIntoStatusExecuteBlock(target);
    ConsFallIntoDamageStatusExecuteBlock(target);
    ConsTacticsExecutedBlock(target);
    ConsAttackExecuteBlock(target);
    //处理伤害执行
    ConsBeforeMakeDamageBlock(target); //造成伤害前（计算伤害前）
    ConsAfterGetDamagedBlock(target);
    ConsGetCureExecuteBlock(target);
}

/*************************************************************消耗持续时间************************************************************************** */

/**
 * 抵御效果
 * @param {*} target 
 */
export const ConsResistBlock = (target)=> {
    let temp = [];
    target.STATUS_RESIST.forEach(block=>{
        if (block.duration > 0) {
            temp.push(block);
            block.duration--;
        }
        else block.InitReport(3,2);
    })
    target.STATUS_RESIST = temp;
}

/**
 * 状态类
 * @param {*} target 
 */
export const ConsStatusBlock = (target)=> {
    let remain = [];
    target.STATUS.forEach(block=>{
        if (block.duration > 0) {
            if (block.waitTime > 0) block.waitTime--;
            remain.push(block);
        }
        else {
            block.InitReport(3,2);
        }
        block.duration -= 1;
    })
    target.STATUS = remain;
}

/**
 * 属性
 * @param {object} target 
 */
export const ConsAttrBlock = (target)=> {
    let remain = [];
    target.ATTR.forEach(block=>{
        if (block.duration > 0){
            block.duration -= 1;
            remain.push(block);
        }
        else block.InitReport(3,2);
    })
    target.ATTR = remain;
}

/**
 * 治疗型状态
 * @param {object} target 
 */
export const ConsCureStatusBlock = (target)=> {
    let remain = [];
    const CURE_STATUS = [...target.CURE_STATUS];
    for (let i in CURE_STATUS) {
        if (CURE_STATUS[i].duration > 0) {
            if (CURE_STATUS[i].waitTime <= 0 && CURE_STATUS[i].isExecute){
                CURE_STATUS[i].Execute(target);
            }
            else CURE_STATUS[i].waitTime--;
            CURE_STATUS[i].duration--;
            remain.push(CURE_STATUS[i]);
        }
        else CURE_STATUS[i].InitReport(3,2);
    }
    target.CURE_STATUS = remain;
}

/**
 * 伤害型状态
 * @param {object} target 
 */
export const ConsDamageStatusBlock = (target)=> {
    let remain = [];
    const SPECIAL_STATUS = [...target.SPECIAL_STATUS];
    for (let i in SPECIAL_STATUS) {
        if (SPECIAL_STATUS[i].duration > 0) {
            if (SPECIAL_STATUS[i].waitTime <= 0) {
                if (SPECIAL_STATUS[i].isExecute) {
                    //执行可能导致target.SPECIAL_STATUS数组长度发生变化
                    SPECIAL_STATUS[i].Execute(target);
                }
            }
            else SPECIAL_STATUS[i].waitTime -= 1;
            remain.push(SPECIAL_STATUS[i]);
            SPECIAL_STATUS[i].duration -= 1;
        }
        else SPECIAL_STATUS[i].InitReport(3,2);
        
    }
    target.SPECIAL_STATUS = remain;
}

/**
 * 增减伤
 * @param {*} target 
 */
export const ConsDamageVarBlock = (target)=> {
    let remain = [];
    target.DAMAGE_VAR.forEach(block=>{
        if (block.duration > 0) {
            if (block.waitTime > 0) block.waitTime--;
            remain.push(block);
            block.duration -= 1;
        }
        else block.InitReport(3,2);
    })
    target.DAMAGE_VAR = remain;
    //处理伤害和治疗数值调整
    let temp = [];
    target.DISCOUNT.forEach(block=>{
        if (block.duration > 0) {
            if (block.waitTime > 0) block.waitTime--;
            temp.push(block);
            block.duration--;
        }
        else block.InitReport(3,2);
    })
    target.DISCOUNT = temp;
}

