/**
 * 获得治疗后执行
 * @param {*} target 
 */
export const ConsGetCureExecuteBlock = (target)=> {
    let remain = [];
    let len = target.GET_CURE.length;
    for (let i = 0; i < len; i++) {
        if (!target.GET_CURE[i]['action']) {
            target.GET_CURE[i]['action'] = true;
            if (target.GET_CURE[i].duration > 0) {
                remain.push(target.GET_CURE[i]);
            }
            target.GET_CURE[i].duration--;
        }
        else remain.push(target.GET_CURE[i]);
        if (target.GET_CURE.length != len) {
            i = 0;
            len = target.GET_CURE.length;
        }
    }
    //清空action
    for (let i in target.GET_CURE) {
        target.GET_CURE[i]['action'] = false;
    }
    target.GET_CURE = remain;
}


/**
 * 消耗stage阶段执行块的持续时间
 * @param {*} target 
 * @param {string} stage "行动开始"....
 */
export const ConsExecuteBlock = (target, stage)=> {
    let remain = [];
    let len = target.EXECUTE.length;
    for (let i = 0; i < len; i++) {
        if (target.EXECUTE[i].executeStage == stage && !target.EXECUTE[i]['action']) {
            target.EXECUTE[i]['action'] = true;
            if (target.EXECUTE[i].duration > 0) {
                target.EXECUTE[i].execute(target.EXECUTE[i]);
                remain.push(target.EXECUTE[i]);
            }
            target.EXECUTE[i].duration--;
        }
        else remain.push(target.EXECUTE[i]);
        if (target.EXECUTE.length != len) {
            i = 0;
            len = target.EXECUTE.length;
        }
    }
    //清空action
    for (let i in target.EXECUTE) {
        target.EXECUTE[i]['action'] = false;
    }
    target.EXECUTE = remain;
}

/**
 * 消耗系统stage阶段执行块的持续时间
 * @param {*} target 
 * @param {string} stage "行动开始"....
 */
export const ConsSysExecuteBlock = (sys,stage)=> {
    let work = sys.EXECUTE.some(block=>{
        if(block.executeStage == stage && stage == "回合结束") return true;
    })
    if(work){
        let r = new Report();
        r.InitSysMsg(sys.global.ReportHeap, "阶段", `回合结束`);
    }
    let remain = [];
    sys.EXECUTE.forEach(block=>{
        if(block.duration > 0){
            if(block.executeStage == stage) block.Executed();
            remain.push(block);
        } 
        else block.duration -= 1;
    })
    sys.EXECUTE = remain;
    
}















/*************************************************************直接消耗掉************************************************************************** */
/**
 * 消耗掉概率最高的规避
 * @param {object} target 
 * @returns 返回true表示成功消耗规避，规避生效
 */
export const ConsDodgeBlock = (target)=> {
    //先消耗掉概率最高的规避块
    let heighestOddDodge = target.DODGE[0];
    let remain = [];
    let suc = false;
    target.DODGE.forEach(block => {
        if(block.rate > heighestOddDodge.rate) heighestOddDodge = block;
    });

    let realOdd = GetMoraleRate(target, heighestOddDodge.rate);
    let rate = Math.floor(Math.random() * 100);
    if (rate < realOdd) {
        let r = new Report();
        r.InitExecuteReport(3, target, `处于规避状态`);
        suc = true;
    }
    else {
        let r = new Report();
        r.InitExecuteReport(3, target, `规避(预备)没有生效，生效几率为${realOdd}%`);
    }
    target.DODGE.forEach(block => {
        if(block != heighestOddDodge) remain.push(block)
    });
    target.DODGE = remain;
    return suc;
}

/**
 * 消耗一次性战法群体目标的增减伤 （目前没有可用情况，未来可能出现情况：下两次主动战法伤害增减）
 * @param {*} target 
 * @param {*} isMakeDamage 
 */
export const ConsOnceTacticsDamageBlock = (target, isMakeDamage)=> {
    let remain = [];
    target.DAMAGE_VAR.forEach(block=>{
        if (block.isOnceTactics && block.isMakeDamage == isMakeDamage) {
            block.InitReport(3,3);
        }
        else remain.push(block);
    })
    target.DAMAGE_VAR = remain;
}

/**
 * 消耗一次性的增减伤控制块
 * @param {*} target 
 * @param {*} damage 
 * @param {*} isComAtk 
 * @param {*} damageType 
 * @param {*} tacticsItem 
 * @param {*} isMakeDamage 
 * @param {*} reportStyle 
 */
export const ConsOnceDamageBlock = (target,dmgId,isComAtk, damageType, tacticsItem, isMakeDamage,reportStyle=1)=> {
    let count = [];
    let remain = [];
    target.DAMAGE_VAR.forEach(block => {
        let typeControl = false;
        if (damageType == block.damageType || block.damageType == '所有') {
            typeControl = true;
        }
        //伤害来源为主动战法，
        else if (tacticsItem != null && tacticsItem.type == '主动' && block.damageType == '主动') typeControl = true;
        //伤害来源为追击战法
        else if (tacticsItem != null && tacticsItem.type == '追击' && block.damageType == '追击') typeControl = true;
        //伤害来源为普通攻击
        else if (tacticsItem == null && isComAtk && block.damageType == '普通攻击') typeControl = true;
        if (block.isOnceAttack && typeControl && block.isMakeDamage == isMakeDamage && !(tacticsItem && block.isOnceTactics)) {
            //出自一个战法的一次性加成块只结算一个，例如鲁肃的下两次减伤是一次只能结算一个
            let flag = false;
            for (let j in count) {
                if (count[j].tactics == block.tactics) {
                    flag = true; break;
                }
            }
            if (flag) remain.push(block);
            //消耗掉一次性块
            else {
                if (block.isOnceAttack || block.isOnceTactics) count.push(block);
                // let r = new Report('状态信息');
                // let str1 = block.isMakeDamage ? "" : "受到";
                // let str2 = "";
                // if (block.damageType == '谋略') str2 = "策略伤害";
                // else if (block.damageType == '攻击') str2 = "攻击伤害";
                // else if (block.damageType == '普通攻击') str2 = "普通攻击伤害";
                // else if (block.damageType == '主动') str2 = "主动战法伤害";
                // else if (block.damageType == '追击') str2 = "追击战法伤害";
                // else if (block.damageType == '所有') str2 = "所有伤害";
                // let str3 = "";
                // if (block.value < -666) str3 = "大幅降低";
                // else if (block.value > 0) str3 = "提高";
                // else str3 = "降低";
                // let str4 = "";
                // if (block.value >= -666) str4 = `${Math.abs(block.value)}%`
                
                // if(reportStyle == 2){
                //     r.InitTwoTargetReport(3, target, `来自【${block.tactics.name}】的${str1 + str2 + str3 + str4}效果消失了`,false);
                // }
                // else r.InitExecuteReport(3, target, `来自【${block.tactics.name}】的${str1 + str2 + str3 + str4}效果消失了`,false);
                let damageInfo = {
                    dmgId : dmgId,
                    block:block,
                    report : null
                }
                target.reportWait.push(damageInfo);
            }
        }
        //不是一次性消耗战法保留
        else remain.push(block);
    });
    target.DAMAGE_VAR = remain;
}






