import Report from "../js/report";

import { LaunchAttack,LaunchActiveTactics } from "../func/action";
import { IsGetStatus } from "../func/judger";
import { ConsBlockDuration, ConsCureStatusBlock, ConsDamageStatusBlock, ConsExecuteBlock } from "../func/handler";

//武将实例
class Warrior{
    constructor(data,position,belong,global,driver){
        this.driver = driver;
        this.global = global;
        this.belong = belong;
        this.position = position;
        this.red = data.red;
        this.sign = data.sign;
        //武将id
        this.id = data.id;
        //是否存活
        this.alive = true;
        //我军
        this.ally = null;
        //敌军
        this.enemy = null;
        //本回合行动力
        this.action = true;
        this.actionStage = 1, // 1表示武将正在行动开始前，2表示武将正在行动，3表示武将行动
        //等级
        this.level = data.level;
        //性别
        this.gender = data.gender;
        //名字
        this.name = data.name;
        //阵营
        this.camp = data.camp;
        //兵种
        this.arms = data.arms;
        //高级兵种的兵种属性
        this.unitType = "";
        //统帅值
        this.cost = data.cost;
        //普通攻击距离
        this.ackRange = data.range;
        //兵力
        this.hp = data.level * 100;
        this.hpMax = data.level * 100;
        this.wound = 0;
        //宝物
        this.artifact = data.artifact;
        //士气
        this.morale = 100;
        //进阶兵种
        this.armsSpec = data.armsSpec
        this.armsCommonSpec = data.armsCommonSpec
        this.upArms = data.upArms;
        this.choseUpArmsIdx = data.choseUpArmsIdx;
        this.choseArmsCharacterIdx = data.choseArmsCharacterIdx
        //属性值
        this.attr = {
            ad : Math.round(data.attr.ad + data.grow.ad * (data.level - 1) + data.plus.ad),
            de : Math.round(data.attr.de + data.grow.de * (data.level - 1) + data.plus.de),
            tact : Math.round(data.attr.tact + data.grow.tact * (data.level - 1) + data.plus.tact),
            speed : Math.round(data.attr.speed + data.grow.speed * (data.level - 1) + data.plus.speed),
        }
        //图像
        this.img = {
            body : data.img.body,
            preview : data.img.preview,
            head : data.img.head,
            banner : data.img.banner,
        }
        //战法栏
        this.TacticsArr = [];
        //战法准备队列
        this.WaitTacticsArr = [];

        this.reportWait = [];

        //属性
        this.ATTR = [];
        //无视属性
        this.IGNORE = [];

        //状态区(非伤害类型)
        this.STATUS = [];
        //特殊类型状态区（恐慌...）
        this.SPECIAL_STATUS = [];
        //治疗类状态
        this.CURE_STATUS = [];
        //特殊攻击（反击分兵）
        this.SPEC_ATTACK = [];

        //状态效果的抵御
        this.STATUS_RESIST = [];

        //攻击距离增减益
        this.RANGE = [];
        //概率变化区
        this.RATE_VAR = [];


        //伤害变化
        this.DAMAGE_VAR = [];
        //治疗效果变化
        this.CURE_VAR = [];
        //状态类伤害变化
        this.STATUS_DAMAGE_VAR = []

        
        
        
        
        
        //规避堆区
        this.DODGE = [];
        //执行堆区
        this.EXECUTE = [];
        //伤害产生前执行（计算伤害阶段开始）
        this.BEFORE_MAKE_DAMAGE = [];
        //即将受到伤害前执行（即将正式受到伤害，可以修改受到的或造成伤害值）
        this.BEFORE_GET_HURT = [];
        //造成伤害后执行
        this.MAKE_DAMAGE = [];
        //受到伤害后执行（受到伤害后）
        this.AFTER_DAMAGE = [];
        //受到持续型伤害后执行
        this.AFTER_CONTINUE_DAMAGE = [];
        //受到恢复效果后执行
        this.GET_CURE = [];
        //发动战法前后执行
        this.ACTIVE_TACTICS_EXECUTE = [];
        //发动普通攻击前后执行
        this.LAUNCH_ATTACK_EXECUTE = [];
        //发动追击战法后
        this.LAUNCH_COMBO_EXECUTE = [];
        
        //对最终伤害或者是治疗量*某数 （刘禅）
        this.DISCOUNT = [];

        //陷入持续伤害状态时执行
        this.FALL_INTO_CONTINUE_DAMAGE = [];
        //陷入特殊状态时执行
        this.BEFORE_FALL_INTO_STATUS = [];
        this.FALL_INTO_STATUS = [];

        //属性被施加前执行
        this.BEFORE_APPLIED_ATTR_EXECUTE = [];
        //属性被施加后执行
        this.AFTER_APPLIED_ATTR_EXECUTE = [];
        

        //目标选择后执行
        this.BEFORE_TARGET_SELECT_EXECUTE = []
        this.AFTER_TARGET_SELECT_EXECUTE = [];

        //结果统计区
        this.STATISTICS = {
            atk : 0,//普通攻击造成伤害统计
            tact : 0,//战法造成伤害统计
            cure : 0,//治疗量统计
        }
        // 复制 app 武将类 特有的属性
        for (const key in data) {
            if (!this.hasOwnProperty(key)) {
                this[key] = data[key];
            }
        }
    }
    //执行自己的回合   
    Execute(){
        this.actionStage = 1;

        let report = new Report();
        report.InitExecuteReport(1,this,`兵力 ${this.hp}`,true,'行动');
        // report.InitSysMsg(this.global.ReportHeap,"行动",`兵力 (${this.hp})`)
        //-----------------------------结算状态控制块-------------------------------------
        ConsExecuteBlock(this,"行动开始前");//结算执行函数块
        
        //状态结算（也包括持续状态和治疗状态的触发）
        ConsBlockDuration(this);

        ConsExecuteBlock(this,"行动开始");//结算执行函数块

        this.actionStage = 2;
        if(!this.alive){
            //结束
            this.action = false;
            return;
        }
        //是否处于洞察
        let insight = IsGetStatus(this,"洞察");
        //判断是否暴走
        if(IsGetStatus(this,"暴走")){
            let r = new Report();
            if(insight) r.InitExecuteReport(2,this,`处于洞察状态，免疫暴走`);
            else r.InitExecuteReport(2,this,`不分敌我目标`);
        }
        
        //判断混乱
        if(IsGetStatus(this,"混乱") && !insight){
            let r = new Report();
            r.InitExecuteReport(2,this,"处于混乱中，无法行动");
        }
        else{
            if(IsGetStatus(this,"混乱") && insight){
                let r = new Report();
                r.InitExecuteReport(2,this,`处于洞察状态，免疫混乱`);
            }
            //试图发动主动战法
            if(IsGetStatus(this,"犹豫") && !insight){
                let r = new Report();
                r.InitExecuteReport(2,this,"处于犹豫中，无法发动主动战法");
            }
            else if(IsGetStatus(this,"无法施法")){
                let r = new Report();
                r.InitExecuteReport(2,this,"无法发动主动战法");
            }
            else{
                if(IsGetStatus(this,"犹豫") && insight){
                    let r = new Report();
                    r.InitExecuteReport(2,this,`处于洞察状态，免疫犹豫`);
                }
                LaunchActiveTactics(this);
            } 
            //发动普通攻击
            if(IsGetStatus(this,"无法普通攻击")){
                let report = new Report();
                report.InitExecuteReport(2,this,"无法进行普通攻击");
            }
            else if(IsGetStatus(this,"怯战") && !insight && !IsGetStatus(this,"免疫怯战")){
                let report = new Report();
                report.InitExecuteReport(2,this,"处于怯战中，无法进行普通攻击");
            }
            else{
                if(IsGetStatus(this,"怯战")){
                    if(insight){
                        let r = new Report();
                        r.InitExecuteReport(2,this,`处于洞察状态，免疫怯战`);
                    }
                    else if(IsGetStatus(this,"免疫怯战")){
                        let r = new Report();
                        r.InitExecuteReport(2,this,`免疫怯战状态`);
                    }
                }
                LaunchAttack(this);
                if(IsGetStatus(this,"连击")){
                    let report = new Report();
                    report.InitExecuteReport(2,this,"执行两次攻击");
                    LaunchAttack(this);
                }
            }
        }
        this.actionStage = 3;
        //结束
        ConsExecuteBlock(this,"行动结束");//结算执行函数块
        this.action = false;
        //伤兵减少
        this.wound = Math.floor(this.wound * 0.87);
        this.actionStage = 4;
    }
}

export default Warrior;