import Report from "../js/report";

import { GetAttr,GetIgnoreValue,GetArmsTypeChar,GetMoraleRate } from "./get";
import { IsGetStatus,IsDodge } from "./judger";
import { ConsDodgeBlock, ConsOnceDamageBlock } from "./handler";


export const AttackDamageIndex = (attacker, damageType, isComAtk, originTactics)=> {
    let res = 0;
    let count1 = [];
    //攻击方结算
    attacker.DAMAGE_VAR.forEach(block=>{
        if (block.isMakeDamage && block.waitTime <= 0) {
            //先检同一个一次性战法只可以被加载一次
            let unlock = true;
            if (block.isOnceAttack) {
                for (let j in count1) {
                    if (count1[j].tactics == block.tactics) unlock = false;
                }
            }
            if (unlock) {
                //伤害来源于战法
                if (originTactics != null) {
                    //指定战法增伤减伤
                    if (block.tacticsItem != null && block.tacticsItem == originTactics) {
                        res += block.value;
                    }
                    //考虑常规攻击还是谋略加成，和主动还是追击战法加成
                    else if ((block.damageType == damageType || block.damageType == originTactics.type) && block.tacticsItem == null) {
                        res += block.value;
                    }
                    else if (block.damageType == '所有') {
                        res += block.value;
                    }
                }
                //伤害来源于普通攻击
                else if (originTactics == null && isComAtk) {
                    if (block.damageType == damageType || block.damageType == '普通攻击') {
                        res += block.value;
                    }
                    else if (block.damageType == '所有') {
                        res += block.value;
                    }
                }
                //伤害来源于反击或其他
                else {
                    if (block.damageType == damageType) {
                        res += block.value;
                    }
                    else if (block.damageType == '所有') {
                        res += block.value;
                    }
                }
                if (block.isOnceAttack) {
                    count1.push(block);
                }
            }
        }
    })
    
    if (res < -90 && res >= - 666) res = 0.1;
    else if (res < -666) res = 0.09;//大幅减伤
    else res = 1 + res / 100;
    // let r = new Report();
    // r.InitExecuteReport(3,attacker,`${res}`);
    return res;
}

const GetIndexOfDamageVar = (attacker, receiver, damageType, isComAtk, orgTact)=> {
    let res = 0;
    let count1 = [];
    let count2 = [];
    //攻击方结算
    attacker.DAMAGE_VAR.forEach((block)=>{
        if(block.isMakeDamage && block.waitTime <= 0){
            let unlock = true;
            if (block.isOnceAttack) {
                for (let j in count1) {
                    if (count1[j].tactics == block.tactics) unlock = false;
                }
            }
            if(unlock){
                //伤害来源于战法
                if (orgTact != null){
                    //指定战法增伤减伤
                    if (block.tacticsItem != null && block.tacticsItem == orgTact) {
                        res += block.value;
                    }
                    //考虑常规攻击还是谋略加成，和主动还是追击战法加成
                    else if ((block.damageType == damageType || block.damageType == orgTact.type) && block.tacticsItem == null) {
                        res += block.value;
                    }
                    else if (block.damageType == '所有') {
                        res += block.value;
                    }
                }
                //伤害来源于普通攻击
                else if (orgTact == null && isComAtk) {
                    if (block.damageType == damageType || block.damageType == '普通攻击') {
                        res += block.value;
                    }
                    else if (block.damageType == '所有') {
                        res += block.value;
                    }
                }
                //伤害来源于反击或其他（暂时不存在这种情况）
                else {
                    if (block.damageType == damageType) {
                        res += block.value;
                    }
                    else if (block.damageType == '所有') {
                        res += block.value;
                    }
                }
                if (block.isOnceAttack) {
                    count1.push(block);
                }
            }
        }
    })
    //防御方结算
    receiver.DAMAGE_VAR.forEach((block)=>{
        if(!block.isMakeDamage && block.waitTime <= 0){
            //先检同一个一次性战法只可以被加载一次
            let unlock = true;
            if(block.isOnceAttack){
                for (let j in count2) {
                    if (count2[j].tactics == block.tactics) unlock = false;
                }
            }
            if (unlock) {
                //伤害来源于战法
                if (orgTact != null) {
                    //指定战法增伤减伤
                    if (block.tacticsItem != null && block.tacticsItem == orgTact) {
                        res += block.value;
                    }
                    //考虑常规攻击还是谋略加成，和主动还是追击战法加成
                    if ((block.damageType == damageType || block.damageType == orgTact.type) && block.tacticsItem == null) {
                        res += block.value;
                    }
                    else if (block.damageType == '所有') {
                        res += block.value;
                    }
                }
                //伤害来源于普通攻击
                else if (orgTact == null && isComAtk) {
                    if (block.damageType == damageType || block.damageType == '普通攻击') {
                        res += block.value;
                    }
                    else if (block.damageType == '所有') {
                        res += block.value;
                    }
                }
                //伤害来源于反击或其他
                else {
                    if (block.damageType == damageType) {
                        res += block.value;
                    }
                    else if (block.damageType == '所有') {
                        res += block.value;
                    }
                }
                if (block.isOnceAttack) {
                    count2.push(block);
                }
            }
        }
    })
    
    if (res < -90 && res >= - 666) res = 0.1;
    else if (res < -666) res = 0.09;//大幅减伤
    else res = 1 + res / 100;

    return res;
}

const GetIndexOfStatusDamageVar = (attacker,receiver,statusDamageType,orgTact)=>{
    let res = 0;
    attacker.STATUS_DAMAGE_VAR.forEach((block)=>{
        if(block.isMakeDamage && block.statusType == statusDamageType){
            if(block.workTactics){
                if(orgTact == block.workTactics) res += block.value;
            }
            else res += block.value;
        }
    })
    receiver.STATUS_DAMAGE_VAR.forEach((block)=>{
        if(!block.isMakeDamage && block.statusType == statusDamageType){
            if(block.workTactics){
                if(orgTact == block.workTactics) res += block.value;
            }
            else res += block.value;
        }
    })
    if (res < -90) res = -90;
    return res / 100;
}

const GetIndexOfCureVar = (initiator, receiver, orgTact)=> {
    let res = 0;
    initiator.CURE_VAR.forEach(block => {
        if (block.isOutput){
            if(block.workTactics){
                if(orgTact == block.workTactics) res += block.value;
            } 
            else res += block.value;
        }
    });
    receiver.CURE_VAR.forEach(block => {
        if (!block.isOutput) res += block.value;
    });
    
    if (res < -90) res = -90;
    return 1 + res / 100;
}

/**
 * 获取最终伤害或治疗量的调整，作用于刘禅等
 * @param {*} attacker 
 * @param {*} receiver 
 * @param {string} isDamageOrCure "伤害" 或 "治疗"
 * @param {*} value 
 * @returns 
 */
const GetFinalDamageOrCureValue = (attacker, receiver, isDamageOrCure, value)=> {
    if (isDamageOrCure != '伤害' && isDamageOrCure != '治疗') {
        console.log('this.isDamageOrCure字段出错', isDamageOrCure);
        return;
    }
    let res = value;
    let ratio = 0;
    let fix = 0;
    attacker.DISCOUNT.forEach(block=>{
        if (block.isDamageOrCure == isDamageOrCure && block.output && block.isRatio) {
            ratio += block.value;
        }
        else if (block.isDamageOrCure == isDamageOrCure && block.output && !block.isRatio) {
            fix += block.value;
        }
    })
    receiver.DISCOUNT.forEach(block=>{
        if (block.isDamageOrCure == isDamageOrCure && !block.output && block.isRatio) {
            ratio += block.value;
        }
        else if (block.isDamageOrCure == isDamageOrCure && !block.output && !block.isRatio) {
            fix += block.value;
        }
    })
    res += fix;
    if (ratio < -90) ratio = -90;
    res = Math.floor(res * (1 + ratio / 100));
    return res < 0 ? 0 : res;
}

/**
 * 计算伤害
 * @param {object} attacker 
 * @param {object} receiver 
 * @param {number} value 
 * @param {string} damageType 伤害大类：攻击或者谋略
 * @param {boolean} isComAtk 是否是普通攻击造成的
 * @param {object} orgTact 伤害来源的战法
 * @param {string} statusDmgType 伤害是哪一种特殊类型的伤害：例如燃烧，火攻，妖术...，没有则默认null
 * @returns 
 */
export const CountDamage = (attacker, receiver, value, damageType = "攻击", isComAtk = true, orgTact = null, statusDmgType = null)=> {
    let dmgId = `${ Date.now()}${Math.random().toString(36).substring(2, 8)}` 
    //确保正确
    let isCommonAtk = isComAtk;
    if (orgTact) {
        isCommonAtk = false;
    }
    //即将造成伤害之前调用执行函数
    attacker.BEFORE_MAKE_DAMAGE.forEach((block) => {
        if (block.isMakeDamage) {
            block.GetDamageData(damageType, isCommonAtk, orgTact, statusDmgType);
            block.Execute(attacker, receiver);
        }
    });
    receiver.BEFORE_MAKE_DAMAGE.forEach((block)=>{
        if(!block.isMakeDamage){
            block.GetDamageData(damageType, isCommonAtk, orgTact, statusDmgType);
            block.Execute(attacker, receiver);
        }
    })

    //基础权值
    let dmgNum = 0;
    let atkAd = GetAttr(attacker, "攻击");
    let recDe = GetAttr(receiver, "防御");
    let atkTact = GetAttr(attacker, "谋略");
    let recTact = GetAttr(receiver, "谋略");


    //攻防差
    let indexAd = atkAd - recDe * (1 - GetIgnoreValue(attacker, '防御'));
    if (indexAd > 0) indexAd = (3 - 500 / (250 + indexAd));
    else indexAd = (100 / (100 - indexAd));
    //谋略差
    let indexTact = recTact * (1 - GetIgnoreValue(attacker, '谋略'));
    //增减伤数值-------------------------------------------
    let varValue = GetIndexOfDamageVar(attacker, receiver, damageType, isCommonAtk, orgTact);
    let statusDmgVarValue = 0;
    if(statusDmgType != null){
        statusDmgVarValue = GetIndexOfStatusDamageVar(attacker,receiver,statusDmgType,orgTact);
    }
    if (indexTact < 50) indexTact = 1;
    else {
        indexTact = (1 - (75 - 9375 / (75 + indexTact)) / 100);
    }
    //兵力基础伤害
    let base = 0;
    if (damageType == '攻击') {
        if (statusDmgType == '动摇') base = 0.33 * (373 * attacker.hp) / (7700 + attacker.hp);
        else base = (373 * attacker.hp) / (7700 + attacker.hp);
    }
    else {
        if (statusDmgType == '燃烧' || statusDmgType == '妖术' || statusDmgType == '恐慌')
            base = 0.33 * (178 * attacker.hp) / (6459 + attacker.hp);
        else base = (178 * attacker.hp) / (6459 + attacker.hp);
    }
    //处于大幅降低情况下base伤害也降低
    if (varValue == 0.09) {
        base *= varValue;
    }
    //策略/攻击基础伤害
    let plus = 0;
    if (damageType == '攻击') {
        if (statusDmgType == '动摇') plus = atkAd * 0.4;
        else plus = atkAd * 0.33;
    }
    else {
        if (statusDmgType == '燃烧' || statusDmgType == '妖术' || statusDmgType == '恐慌') plus = atkTact * 0.25;
        else plus = atkTact * 0.5 * indexTact;
    }
    plus = plus * (varValue + statusDmgVarValue);
    //主要伤害
    let main = 0;
    if (damageType == '攻击') {
        main = (value / 100) * (300 * attacker.hp) / (3500 + attacker.hp) * indexAd;
    }
    else {
        main = (value / 100) * (300 * attacker.hp) / (3500 + attacker.hp) * indexTact;
    }
    main = main * (varValue + statusDmgVarValue);
    //总伤害
    dmgNum = base + plus + main;

    //兵种克制
    if (!IsGetStatus(attacker, "无视兵种")) {
        let atkArms = GetArmsTypeChar(attacker.upArms[attacker.choseUpArmsIdx],true);
        let recArms = GetArmsTypeChar(receiver.upArms[receiver.choseUpArmsIdx],true);
        let map = {
            'b-q': 0.7,  // 步兵打骑兵
            'q-g': 0.7,  // 骑兵打弓兵
            'g-b': 0.7   // 弓兵打步兵
        };
        const key = `${atkArms}-${recArms}`;
        dmgNum *= map[key] || 1;
    }
    dmgNum = GetFinalDamageOrCureValue(attacker, receiver, '伤害', dmgNum);
    //判断规避
    let isDodge = IsDodge(receiver);
    if (isDodge && !IsGetStatus(attacker, "无视规避")) {
        let work = ConsDodgeBlock(receiver);
        if(work) dmgNum = 0;
    }
    else if (IsGetStatus(attacker, "无视规避") && isDodge) {
        let reportL = 3;
        if(attacker.driver.turn < 1) reportL = 2
        let r = new Report();
        r.InitExecuteReport(reportL, attacker, `无视规避，规避无效`);
    }
    //消耗掉一次性的增减伤加成块
    ConsOnceDamageBlock(attacker,dmgId,isComAtk, damageType, orgTact, true,3);
    ConsOnceDamageBlock(receiver,dmgId,isComAtk, damageType, orgTact, false,3);
    //返回伤害
    if (dmgNum < 0) dmgNum = 0;
    return [Math.floor(dmgNum),dmgId];

}

/**
 * 计算治疗量
 * @param {object} initiator 
 * @param {object} receiver 
 * @param {number} cureValue 
 * @param {number} cureIndex 
 * @param {object|null} tacticsItem 
 * @param {number|null} fixedCureValue 治疗量是否是一个固定数值,默认null,例如吸血效果获取目标伤害的比例的兵力，不靠谋略属性影响
 * @returns 
 */
export const CountCure = (initiator, receiver, cureValue, cureIndex, tacticsItem = null,fixedCureValue=null)=> {
    //initiator发起该恢复效果的武将
    //receiver是接受恢复效果的武将
    let cure = 0;
    if(fixedCureValue){
        cure = fixedCureValue;
    }
    else{
        let index = (GetAttr(initiator, "谋略") - 80) < 0 ? 0 : (GetAttr(initiator, "谋略") - 80);
        let base = cureValue + cureIndex * index;
        cure = (300 * receiver.hp) / (3500 + receiver.hp);
        cure *= (base / 100);
    }
    //获取治疗增减效果
    let cureVarIndex = GetIndexOfCureVar(initiator, receiver, tacticsItem);
    cure = cure * cureVarIndex;
    cure = GetFinalDamageOrCureValue(initiator, receiver, '治疗', cure);
    // cure = GetFinalDamageOrCureValue(initiator, receiver, '治疗', cure);
    cure = Math.floor(Math.abs(cure));
    return cure;
}