                let damage = this.damageValue;
                let getNewHurtValue = Math.floor(divideRatio / 100 * damage);
                let r = new Report();
                r.InitTwoTargetReport(3, self, this.receiver, `【言出必克】为`, `分摊${divideRatio}%策略伤害(${getNewHurtValue})`)
