import Report from "../js/report";
//控制战法的发动几率和是否跳过准备阶段
class RateBlock {
    constructor(applicator, tactics, workTactics, value, duration, waitTime, skipTime) {
        //来自谁
        this.applicator = applicator;
        //施加该效果的战法
        this.tactics = tactics;
        //获得发动几率变化战法
        this.workTactics = workTactics;
        //几率增减数值
        this.value = value;
        //持续时间
        this.duration = duration;
        //等待事件
        this.waitTime = waitTime;
        //跳过准备时间
        this.skipTime = skipTime == undefined ? 0 : skipTime;
        //是否为增益效果
        this.isBenefit = false;
        this.beExecuted = null;
        if (this.value > 0 || this.skipTime > 0) this.isBenefit = true;
    }
    Init(target, needReport = true, reportLevel = 3, reportStyle = 1) {
        this.beExecuted = target;
        //检测冲突
        const abort = target.RATE_VAR.some(block => {
            if (block.tactics.type == this.tactics.type && block.workTactics == this.workTactics) {
                let r = new Report();
                if (this.skipTime == 0) {
                    let txt = `已存在【${this.workTactics.name}】的发动几率${this.value > 0 ? "提高" : "降低"}效果`
                    r.InitExecuteReport(3, target, txt)
                    return true;
                }
                else if (this.skipTime > 0 && block.skipTime > 0
                    || this.skipTime < 0 && block.skipTime < 0) {
                    let txt = `已存在【${this.workTactics.name}】的准备时间${this.skipTime > 0 ? "增加" : "减少"}${Math.abs(this.skipTime)}回合效果`
                    r.InitExecuteReport(3, target, txt);
                    return true;
                }
            }
        })
        if (abort) return false;
        target.RATE_VAR.push(this);
        if (needReport) {
            this.InitReport(reportStyle,reportLevel);
        }
        return true;
    }
    /**
     * 生成战报
     * @param {*} reportStyle 1: A武将【x】发动几率提高x% 2: A武将【x】令B武将【x】的发动几率提高x% 3: A武将【x】发动几率提高x%效果消失了 4: A武将【x】的发动几率提高x%效果被移除了 5:A武将【x】移除了B武将【x】的发动几率提高x%效果
     * @param {*} reportLevel 
     * @param {*} newApplicator 
     * @param {*} newTact 
     */
    InitReport(reportStyle = 1, reportLevel = 3, newApplicator = null, newTact = null) {
        if(this.value){
            let r1 = new Report();
            let msg1 = `【${this.workTactics.name}】发动几率${this.isBenefit ? "提高" : "降低"}`;
            msg1 += this.value ? `${Math.abs(this.value)}%` : "";
            if(this.beExecuted.driver.turn < 1) msg1 += "效果已施加"
            if (reportStyle == 1) r1.InitExecuteReport(reportLevel, this.beExecuted, msg1);
            if (reportStyle == 2) r1.InitTwoTargetReport(reportLevel, this.applicator, this.beExecuted, `【${this.tactics.name}】令`, msg1);
            if(reportStyle == 3){
                r1 = new Report("状态信息");
                r1.InitExecuteReport(reportLevel, this.beExecuted, msg1 + "效果消失了");
            }
            if (reportStyle == 4) r1.InitExecuteReport(reportLevel, this.beExecuted, msg1 + "效果被移除了");
            if (reportStyle == 5 && newApplicator && newTact) r1.InitTwoTargetReport(reportLevel, newApplicator, this.beExecuted, `【${newTact.name}】移除了`, msg1+"效果");
        }

        if(this.skipTime){
            let r = new Report();
            let msg = `【${this.workTactics.name}】准备时间${this.isBenefit ? "增加" : "跳过"}`;
            msg += this.skipTime ? `${Math.abs(this.skipTime)}回合` : ``;
            if(this.beExecuted.driver.turn < 1) msg += "效果已施加"
            if (reportStyle == 1) r.InitExecuteReport(reportLevel, this.beExecuted, msg);
            if (reportStyle == 2) r.InitTwoTargetReport(reportLevel, this.applicator, this.beExecuted, `【${this.tactics.name}】令`, msg);
            if(reportStyle == 3){
                r = new Report("状态信息");
                r.InitExecuteReport(reportLevel, this.beExecuted, msg + "效果消失了");
            }
            if (reportStyle == 4) r.InitExecuteReport(reportLevel, this.beExecuted, msg + "效果被移除了");
            if (reportStyle == 5 && newApplicator && newTact) r.InitTwoTargetReport(reportLevel, newApplicator, this.beExecuted, `【${newTact.name}】移除了`, msg+"效果");
        }
    }
    SelfDestroy() {
        let temp = []
        for (let i in this.beExecuted.RATE_VAR) {
            if (this.beExecuted.RATE_VAR[i] != this) temp.push(this.beExecuted.RATE_VAR[i]);
        }
        this.beExecuted.RATE_VAR = temp;
    }
}

export default RateBlock;