import Report from "../js/report";


export class ResistStatusBlock{
    constructor(applicator,tactics,resistType,rate,duration,waitTime){
        //施加该块的武将
        this.applicator = applicator;
        //施加该战法的类型
        this.tactics = tactics;
        //需要抵御的状态类型
        this.resistType = resistType;
        //触发几率
        this.rate = rate || 120;
        //持续实践
        this.duration = duration;
        this.waitTime = waitTime
        //执行人
        this.beExecuted = null;
    }
    Init(target,isNeedReport=true,reportLevel=3,reportStyle=1){
        this.beExecuted = target;
        target.STATUS_RESIST.push(this)
        if(isNeedReport){
            this.InitReport(reportStyle,reportLevel)
        }
    }   
    /**
     * 战报生成
     * @param {*} reportStyle 1: A武将抵御x状态效果已施加  2:A武将【x】令B武将可以抵御x状态 3: A武将抵御x状态效果消失了 4: A武将抵御x状态效果被移除了 5:A武将【x】移除了B武将抵御x的效果 
     * @param {*} reportLevel 
     * @param {*} newApplicator 
     * @param {*} newTact 
     */
    InitReport(reportStyle = 1,reportLevel = 3,newApplicator = null, newTact = null){
        let r = new Report();
        let msg = `抵御${resistType}状态`
        if(reportStyle == 1) r.InitExecuteReport(reportLevel,this.beExecuted,msg+"效果已施加");
        if(reportStyle == 2) r.InitTwoTargetReport(reportLevel,this.applicator,this.beExecuted,`【${this.tactics.name}】令`,'可以'+msg);
        if(reportStyle == 3) {
            r = new Report("状态信息");
            r.InitExecuteReport(reportLevel,this.beExecuted,msg+"效果消失了");
        }
        if(reportStyle == 4) r.InitExecuteReport(reportLevel,this.beExecuted,msg+"效果被移除了");
        if(reportStyle == 5 && newApplicator && newTact) r.InitTwoTargetReport(reportLevel,newApplicator,this.beExecuted,`【${newTact.name}】移除了`,msg+"效果");
    }
}