
import Report from "../js/report";

import { GetDivideForceTarget, GetMoraleRate, GetRange,GetTarget,GetRealTacticsRate, GetRealTacticsPrepareTime } from "./get";
import { CountDamage } from "./cacl";
import { Hurt } from "./processor";
import { IsGetStatus,IsCanAttackSomeone } from "./judger";
import { ConsOnceTacticsDamageBlock, HandlerWaitTactics } from "./handler";


/**
 * 试图发动主动战法
 * @param {Object} target 试图发动主动战法的武将 
 */
export const LaunchActiveTactics = (target)=> {
    target.TacticsArr.forEach((tact)=>{
        if (tact.type == '主动') {
            //触发试图发动阶段
            target.EXECUTE.forEach((block)=>{
                if(block.executeStage == "试图发动主动战法") block.execute();
            })
            //发动准备战法,launchTactics发动的准备型战法
            let launchTactics = HandlerWaitTactics(target, tact);
            let isLaunched = launchTactics.launch == null ? false : true;
            let rate = GetRealTacticsRate(target, tact);
            let rand = Math.floor(Math.random() * 100);
            if (rand < rate) {
                //瞬发战法直接发动
                if (GetRealTacticsPrepareTime(target, tact) <= 0 && !isLaunched) {
                    let report = new Report();
                    report.InitExecuteReport(2, target, `发动【${tact.name}】!`)
                    tact.execute(target);
                    isLaunched = true;
                }
                //准备型战法进入准备队列
                else if (launchTactics.wait == null && launchTactics.launch == null) {
                    let r = new Report();
                    r.InitExecuteReport(2, target, `【${tact.name}】开始准备`);
                    let block = {
                        tactics: tact,//战法实体
                        waitTime: GetRealTacticsPrepareTime(target, tact)//战法等待时间
                    }
                    target.WaitTacticsArr.push(block);
                }
            }
            else if (launchTactics.wait == null && launchTactics.launch == null) {
                let r = new Report();
                r.InitExecuteReport(2, target, `【${tact.name}】未发动，发动几率为${rate}%`)
            }
            //消耗掉执行下一次战法增伤
            if (tact.isDamageMaker && isLaunched) ConsOnceTacticsDamageBlock(target, true);
            //发动战法后调用执行函数
            target.ACTIVE_TACTICS_EXECUTE.forEach(block=>{
                //发动成功就匹配成功发动后执行，失败就匹配发动失败后执行，实现（乘间击隙）
                if (block.isLaunchedExecute == isLaunched && block.waitTime <= 0  && block.isAfterExecute && block.tacticsType == '主动') {
                    block.Execute(tact);
                }
            })
        }
    })
}


/**
 * 发动普通攻击
 * @param {Object} attacker 发动该次普通攻击的武将
 */
export const LaunchAttack = (attacker)=> {
    let adRange = GetRange(attacker,null);
    let receivers = GetTarget(attacker, adRange, false, true, 1, false, true, null,"");
    //计算伤害
    if (receivers.length > 0) {
        let receiver = receivers[0]
        //战报填入
        let report = new Report();
        report.InitTwoTargetReport(2, attacker, receiver, "对", "发动普通攻击");
        //执行普通攻击前执行函数
        attacker.LAUNCH_ATTACK_EXECUTE.forEach(block => {
            if (block.duration > 0 && !block.isAfterExecute) {
                block.Execute(receiver);
            }
        });

        let [damage ,dmgId] = CountDamage(attacker, receiver, 100, "攻击",true,null,null);
        Hurt(dmgId,attacker, receiver, damage, true, "攻击", null,true,null,false);
        //执行普通攻击后执行函数
        attacker.LAUNCH_ATTACK_EXECUTE.forEach(block => {
            if (block.duration > 0 && block.isAfterExecute && block.detailExecuteTime == '造成伤害后') {
                block.Execute(receiver);
            }
        });
        //启动目标反击
        if (receiver.alive && !IsGetStatus(attacker,'不触发反击')) LaunchSpecialAttack(attacker, receiver, "反击");
        //执行分兵
        LaunchSpecialAttack(attacker, receiver, "分兵");
        attacker.LAUNCH_ATTACK_EXECUTE.forEach(block => {
            if (block.duration > 0 && block.isAfterExecute && block.detailExecuteTime == '分兵后') {
                block.Execute(receiver);
            }
        });
        //执行追击
        if (receiver.alive) LaunchComboAttack(attacker, receiver);
        //执行普通攻击后执行函数
        attacker.LAUNCH_ATTACK_EXECUTE.forEach(block => {
            if (block.duration > 0 && block.isAfterExecute && block.detailExecuteTime == '追击后') {
                block.Execute(receiver);
            }
        });
    }
    else {
        //战报填入
        let report = new Report();
        report.InitExecuteReport(2, attacker, "攻击范围内没有目标")
    }

}


/**
 * 发动特殊攻击
 * @param {object} attacker 
 * @param {object} receiver 
 * @param {string} type 类型：“反击”，“分兵”，他们都算普通攻击
 */
export const LaunchSpecialAttack = (attacker, receiver, type = '反击')=> {
    if (type == "反击") {
        receiver.SPEC_ATTACK.forEach((block)=>{
            if (block.waitTime <= 0 && block.type == '反击') {
                let avail = IsCanAttackSomeone(receiver, attacker);
                if (avail) {
                    let rate = GetMoraleRate(receiver,block.rate);
                    let rand = Math.floor(Math.random() * 100);
                    //发动反击
                    if (rand < rate) {
                        let r = new Report();
                        r.InitExecuteReport(3, receiver, `进行反击`);
                        let [damage ,dmgId] = CountDamage(receiver, attacker, block.damageValue, "攻击", true, null, null);
                        Hurt(dmgId,receiver, attacker, damage, true, "攻击", null,true,null,false);
                    }
                }
                else {
                    let r = new Report();
                    r.InitExecuteReport(3, receiver, `由于射程不足，无法反击`);
                }

            }
        })
    }
    else if (type == "分兵") {
        attacker.SPEC_ATTACK.forEach(block => {
            if (block.waitTime <= 0 && block.type == '分兵') {
                let rate = GetMoraleRate(attacker,block.rate);
                let rand = Math.floor(Math.random() * 100);
                if (rand < rate) {
                    let targetArr = GetDivideForceTarget(receiver);
                    targetArr.forEach(tgt => {
                        let r = new Report();
                        r.InitExecuteReport(3, attacker, `执行分兵`);

                        let [damage ,dmgId] = CountDamage(attacker, tgt, block.damageValue, "攻击", true, null, null);
                        Hurt(dmgId,attacker, tgt, damage, true, "攻击", null,true,null,false);
                    });
                }
            }
        });
    }
}


/**
 * 试图发动追击战法
 * @param {Object} attacker -发动追击战法的武将
 * @param {Object} receiver -受到发动追击战法攻击的武将
 */
export const LaunchComboAttack = (attacker, receiver)=> {
    attacker.TacticsArr.forEach(tact => {
        if(tact.type == "追击"){
            //触发试图发动阶段
            attacker.EXECUTE.forEach(block => {
                if(block.executeStage == "试图发动追击战法") block.execute()
            });
            let realOdd = GetRealTacticsRate(attacker, tact);
            let rand = Math.floor(Math.random() * 100);
            if (rand < realOdd) {
                let r = new Report();
                r.InitExecuteReport(3, attacker, `的攻击发动【${tact.name}】！`);
                tact.execute(attacker, receiver);
                //发动追击战法后调用执行函数
                attacker.ACTIVE_TACTICS_EXECUTE.forEach(block => {
                    if (block.waitTime <= 0 && block.isAfterExecute && block.tacticsType == '追击') {
                        block.Execute(tact);
                    }
                });

                attacker.LAUNCH_COMBO_EXECUTE.forEach(block=>{
                    if(block.duration >= 0){
                        block.GetData(attacker,receiver,tact);
                        block.Execute();
                    }
                })
            }
            else {
                let r = new Report();
                r.InitExecuteReport(3, attacker, `的攻击未发动【${tact.name}】，发动几率为${realOdd}%`);
            }
        }
    });
}