
import Report from "../js/report";


import { IsGetStatus } from "./judger";
import { isObjectInArray, pushObjectNoRepeat } from "./util";


const attrMap = {
    "攻击": "ad",
    "谋略": "tact",
    "防御": "de",
    "速度": "speed",
}
const armTypeMap = {
    "禁卫": "b",
    "长枪兵": "b",
    "重步兵": "b",
    "藤甲兵": "b",
    "蛮兵": "b",
    "长弓兵": "g",
    "弩兵": "g",
    "死士": "g",
    "轻骑兵": "q",
    "重骑兵": "q",
    "铁骑兵": "q",
    "弓骑兵": "q",
    "象兵": "q"
}

const continueStatus = ["恐慌", "妖术", "动摇", "燃烧", "火攻", "同心"];
const controlStatus = ["怯战", "犹豫", "混乱", "暴走"];

const statusMap = {
    "怯战": "STATUS", "犹豫": "STATUS", "混乱": "STATUS", "暴走": "STATUS", "围困": "STATUS",
    "妖术": "SPECIAL_STATUS", "恐慌": "SPECIAL_STATUS", "火攻": "SPECIAL_STATUS", "燃烧": "SPECIAL_STATUS", "动摇": "SPECIAL_STATUS",
}

export const GetStatusRemainTimeByTactics = (target, whichStatus, tactics) => {
    if (!statusMap[whichStatus]) {
        return -1;
    }
    for (let i in target[statusMap[whichStatus]]) {
        let block = target[statusMap[whichStatus]][i];
        if (block.tactics == tactics) return block.duration;
    }
    // return target[statusMap[whichStatus]].some(block => {
    //     return block.tactics == tactics; 
    // });
    return -1;
}

/**
 * 获取当前回合的需要执行的武将
 * @param {Array} allTeam 两个队伍
 * @returns object目标武将
 */
export const GetExecutor = (allTeam) => {
    let res = null;
    let fast = -1000;
    allTeam.forEach(team => {
        team.forEach(warrior => {
            if (warrior.action && warrior.alive) {
                let speed = GetAttr(warrior, "速度");
                if (IsGetStatus(warrior, "先手")) speed += 1000;
                if (speed > fast) {
                    res = warrior;
                    fast = speed;
                }
                else if (speed == fast) {
                    let rand = Math.floor(Math.random() * 2);
                    if (rand == 1) {
                        res = warrior;
                        fast = speed;
                    }
                }
            }
        })
    })
    return res;
}



/**
 * 获取武将被施加控制状态的数量
 * @param {object} target 目标
 * @returns 返回数目
 */
export const GetNumberOfControlStatus = (target) => {
    let count = 0;
    target.STATUS.forEach(block => {
        if (controlStatus.includes(block.type) && block.waitTime <= 0) count += 1;
    });
    return count;
}
/**
 * 获取武将被施加伤害类状态的数量
 * @param {object} target 目标 
 * @returns 返回数目
 */
export const GetNumberOfDamageStatus = (target) => {
    let count = 0;
    target.SPECIAL_STATUS.forEach(block => {
        if (continueStatus.includes(block.type) && block.waitTime <= 0) {
            count += 1;
        }
    });
    return count;
}

/**
 * 输入几率，返回士气加成下的几率
 * @param {object} target 
 * @param {number} rate 几率 
 * @returns 返回实际几率
 */
export const GetMoraleRate = (target, rate) => {
    let res = rate;
    //士气影响
    let addition = Math.abs(target.morale - 100);
    addition = addition / (100 + target.morale / 2);
    if (target.morale < 100) {
        res -= res * addition;
    } else {
        res += res * addition;
    }
    res = Math.floor(res);
    return res;
}

/**
 * 输入基础数值和受影响的属性和index值，返回加成值
 * @param {object} target 目标
 * @param {number} baseValue 基础数值
 * @param {string} attrType 影响属性
 * @param {number} index 加成指数
 * @returns 
 */
export const GetAdditionValue = (target, baseValue, attrType, index, fixedNum = 0) => {
    let newValue = GetAttr(target, attrType);
    newValue = newValue < 80 ? 0 : newValue - 80;
    newValue = newValue * index + baseValue;
    newValue = fixedNum > 0 ? Number(newValue.toFixed(fixedNum)) : Math.round(newValue);
    return newValue;
}

/**
 * 获取武将的属性
 * @param {object} target 
 * @param {string} attrType 
 * @returns 
 */
export const GetAttr = (target, attrType) => {
    let res = 0;
    //基础属性
    res = target.attr[attrMap[attrType]];
    //读取属性堆加区的块
    target.ATTR.forEach(block => {
        if (block.attrType == attrType) {
            res += block.value;
        }
    });
    return res < 0 ? 0 : res;
}

/**
 * 获取选择的范围距离
 * @param {object} target 
 * @param {object} tacticsItem 战法实体,tacticsItem=null表示获取普通攻击距离范围（defualt=null）
 * @returns 
 */
export const GetRange = (target, tacticsItem = null) => {
    let distance = tacticsItem ? tacticsItem.range : target.ackRange;
    target.RANGE.forEach(block => {
        if (tacticsItem && !block.isCommonAttack && block.waitTime <= 0) {
            if (block.workTactics && block.workTactics == tacticsItem) {
                distance += block.value;
            }
            else distance += block.value;
        }
        else if (block.isCommonAttack && block.waitTime <= 0) {
            distance += block.value;
        }
    });
    return distance;
}

/**
 * 获取一个武将无视属性的值
 * @param {object} target 
 * @param {string} attrType 需要读取的属性："攻击";"谋略";"防御";"速度"
 * @returns 返回 0-1,0.5表示无视50%的某属性
 */
export const GetIgnoreValue = (target, attrType) => {
    let res = 0;
    target.IGNORE.forEach(block => {
        if (block.ignoreAttrType == attrType) {
            res += block.value;
        }
    });
    if (res > 99) res = 99;
    return res / 100;
}

/**
 * 输入高级兵种，获取武将兵种的类型
 * @param {string} orgArmStr 高级兵种字符串，例如禁卫...
 * @param {*} isTrueArmType 是否考虑其实际增减伤效果的兵种，例如长枪兵具有弓兵的特性
 * @returns 返回 "g",'b','q'
 */
export const GetArmsTypeChar = (orgArmStr, isTrueArmType = false) => {
    let armStr = armTypeMap[orgArmStr];
    if (isTrueArmType && orgArmStr == "长枪兵") armStr = "g"
    return armStr
}





/**
 * 获取self武将和target武将之间的距离
 * @param {object} self self武将，即函数调用者
 * @param {object} target 目标武将
 * @returns 返回距离
 */
export const GetDistance = (self, target) => {
    let distance = 0;
    if (self == target) return distance;
    if (target.belong == self.belong) {
        if (self.position == 1) distance = 1
        else if (self.ally[1].alive) distance = 2
        else distance = 1
    }
    else {
        for (let i = self.position; i < 3; i++) {
            if (self.ally[i].alive && self.ally[i] != self) distance += 1;
        }
        for (let i = target.ally.length - 1; i >= 0; i--) {
            if (target.ally[i] != target && target.ally[i].alive) distance += 1;
            else if (target.ally[i] == target) {
                distance += 1;
                break;
            }
        }
    }
    return distance;
}

/**
     * 获取一个战法的实际准备时间
     * @param {object} target  目标
     * @param {*} targetTactics 
     * @returns 
     */
export const GetRealTacticsPrepareTime = (target, targetTactics) => {
    let base = targetTactics.wait;
    target.RATE_VAR.forEach(block => {
        if (block.workTactics == targetTactics) {
            base -= block.skipTime;
        }
    })
    if (base < 0) base = 0;
    return base;
}

/**
 * 获得武将谋战法的实际发动几率
 * @param {object} target 
 * @param {object} tacticsItem 
 * @returns 
 */
export const GetRealTacticsRate = (target, tacticsItem) => {
    let rate = GetMoraleRate(target, tacticsItem.rate);
    target.RATE_VAR.forEach(block => {
        if (block.workTactics == tacticsItem) rate += block.value;
    });
    return rate > 100 ? 100 : rate;
}




/*************************************************************获取目标************************************************************************************ */

/**
 * 获取team中兵力最type的武将
 * @param {Array} team 
 * @param {string|'max'} type max 和 min
 */
export const GetExtremeTroopTarget = (team, type = 'max') => {
    let target = null;
    team.forEach(current => {
        if (!current?.alive) return;
        if (target == null) {
            target = current;
            return;
        }
        if (type == 'max' && current.hp > target.hp) {
            target = current;
        }
        else if (type == 'min' && current.hp < target.hp) {
            target = current;
        }
        else if (current.hp == target.hp) {
            let rand = Math.floor(Math.random() * 2);
            if (rand == 1) target = current;
        }
    })
    return target;
}


/**
 * 在whichTeam中选择出attrType最(低或者高)的武将
 * @param {Array} whichTeam 
 * @param {string} attrType 
 * @param {boolean|true} isSelectLow 是否选择属性最低的，反之选择最高的
 * @returns 
 */
export const GetMaxAttrValueTarget = (whichTeam, attrType, isSelectLow = true) => {
    let maxTarget = null;
    let maxValue = 0;
    whichTeam.forEach(current => {
        if (!current?.alive) return;
        let attrValue = GetAttr(current, attrType);
        if (maxTarget == null) {
            maxTarget = current;
            maxValue = attrValue
        }
        else {
            if (isSelectLow && attrValue < maxValue) {
                maxTarget = current;
                maxValue = attrValue;
            }
            else if (!isSelectLow && attrValue > maxValue) {
                maxTarget = current;
                maxValue = attrValue;
            }
            else if (attrValue == maxValue) {
                let rand = Math.floor(Math.random() * 2);
                if (rand == 1) {
                    maxTarget = current;
                    maxValue = attrValue;
                }
            }
        }
    })
    return maxTarget;
}

/**
 * 获取分兵的目标对象，可用于多种aoe选中场景
 * @param {Object} target 获取目标周围的武将作为分兵攻击攻击的对象
 * @returns 返回目标数组
 */
export const GetDivideForceTarget = (target) => {
    let res = [];
    //选择前面的
    for (let i = Number(target.position) - 1; i >= 0; i--) {
        if (target.ally[i].alive) {
            res.push(target.ally[i])
            break;
        }
    }
    //选择后面的
    for (let i = Number(target.position) + 1; i < 3; i++) {
        if (target.ally[i].alive) {
            res.push(target.ally[i]);
            break;
        }
    }
    return res;
}

/**
 * 选择目标
 * @param {*} self 
 * @param {*} range 
 * @param {*} isFri 
 * @param {*} isEn 
 * @param {*} tgtNumber 
 * @param {*} includeSelf 
 * @param {*} considerMad 
 * @param {*} tacticsItem 
 * @param {Array} forWhat 选择这些目标的目的,可多选(例如[1,3]):0.没有意义  1.造成攻击伤害 2.造成谋略伤害 3.造成持续型伤害 4.造成恢复 5.造成控制 6.造成围困
 * @param {*} selectTgts 指定选择这些目标（即选择的目标中必须包含这些）
 * @returns 
 */
export const GetTarget = (self, range, isFri, isEn, tgtNumber, includeSelf, considerMad = true, tacticsItem = null, forWhat = [], selectTgts = null) => {
    let res = [];//存放最终结果
    let arr = [];//中间结果
    let realRange = range;
    let realTgtNum = tgtNumber; //还要选择多少个目标
    if (selectTgts != null) {
        res = selectTgts;
        realTgtNum -= selectTgts.length > realTgtNum ? realTgtNum : selectTgts.length; //如果已经指定好目标了，就不要再选择目标了
    }
    //执行选择目标前执行函数(修改选择目标的个数)
    for (let i in self.BEFORE_TARGET_SELECT_EXECUTE) {
        self.BEFORE_TARGET_SELECT_EXECUTE[i].Execute(self, tacticsItem, range, realTgtNum, res, forWhat);
        realTgtNum = self.BEFORE_TARGET_SELECT_EXECUTE[i].tgtNumber;
    }


    //还需要选择目标才继续
    if (realTgtNum > 0) {
        let isMad = IsGetStatus(self, "暴走");
        if (IsGetStatus(self, "洞察")) isMad = false;
        //放入可选范围的敌军
        if ((considerMad && isMad) || isEn) {
            self.enemy.forEach(tgt => {
                if (!tgt?.alive || GetDistance(self, tgt) > realRange) {
                    return
                }
                if (tacticsItem != null && tacticsItem.type == '指挥' && (IsGetStatus(tgt, '不受敌方指挥战法影响') || IsGetStatus(tgt, '不受指挥战法影响'))) {
                    let r = new Report();
                    r.InitExecuteReport(3, tgt, `不受敌方指挥战法影响`);
                    return
                }
                if (!res.includes(tgt)) pushObjectNoRepeat(arr, tgt);
            })
        }
        //放入可选范围内友军
        if ((considerMad && isMad) || isFri) {
            self.ally.forEach(tgt => {
                // 1. 基础过滤：排除已死亡或超出距离的目标
                if (!tgt?.alive || GetDistance(self, tgt) > realRange) {
                    return;
                }
                // 处理自身情况（如果不包含自己，则跳过）
                if (tgt === self && !includeSelf) {
                    return;
                }
                // 处理指挥战法免疫的情况
                if (tacticsItem?.type === '指挥' && IsGetStatus(tgt, '不受指挥战法影响')) {
                    const r = new Report();
                    r.InitExecuteReport(3, tgt, `不受指挥战法影响`);
                    return; // 已处理，无需加入数组
                }
                if (!res.includes(tgt)) pushObjectNoRepeat(arr, tgt);
            })
        }

        //判断挑衅，普通攻击才会触发挑衅
        if (tacticsItem == null && IsGetStatus(self, '挑衅') && !IsGetStatus(self, '洞察')) {
            //查看arr中是否含有挑衅者
            let target = null;
            for (let i in self.STATUS) {
                if (self.STATUS[i].type == '挑衅') {
                    target = self.STATUS[i].applicator;
                    break;
                }
            }
            for (let i in arr) {
                if (arr[i] == target) {
                    res.push(arr[i]);
                    arr = [];
                    let r = new Report();
                    r.InitTwoTargetReport(2, self, target, `受到`, `挑衅`);
                    break;
                }
            }
        }
        //从结果数组中挑选number个放入res
        for (let i = 0; i < realTgtNum && arr.length > 0; i++) {
            let rand = Math.floor(Math.random() * arr.length);
            res.push(arr[rand]);
            arr.splice(rand, 1);
        }
        //判断援护,只在普通攻击时判定
        if (tacticsItem == null) {
            let temp = [];
            for (let i in res) {
                //援护者
                let target = null;
                for (let j in res[i].STATUS) {
                    if (res[i].STATUS[j].type == '援护') {
                        target = res[i].STATUS[j].applicator;
                        break;
                    }
                }
                if (target != null) {
                    let r = new Report();
                    r.InitTwoTargetReport(2, res[i], target, `受到`, `援护`);
                    temp.push(target);
                }
                else temp.push(res[i]);
            }
            res = temp;
        }
    }

    //对结果进行按位置排序
    for (let i = 0; i < res.length; i++) {
        for (let j = i + 1; j < res.length; j++) {
            if (Number(res[i].position) > Number(res[j].position)) {
                let temp = res[i];
                res[i] = res[j];
                res[j] = temp;
            }
        }
    }

    //执行选择目标后执行函数
    for (let i in self.AFTER_TARGET_SELECT_EXECUTE) {
        self.AFTER_TARGET_SELECT_EXECUTE[i].Execute(self, tacticsItem, range, realTgtNum, res, forWhat);
    }

    return res;
}



