import Report from "../js/report";

import { CountCure, CountDamage } from "../func/cacl";
import { IsGetStatus } from "../func/judger";
import { Cure, Hurt } from "../func/processor";
import { GetMoraleRate } from "../func/get";


const benefitStatus = ["连击","援护","洞察","先手","同心","无视兵种","无视规避","免疫怯战","免疫犹豫","免疫暴走","免疫混乱","免疫围困","不受敌方指挥战法影响","不触发反击"]




export class StatusBlock {
    /**
     * 状态类型块
     * @param {object} applicator 
     * @param {object} tactics 
     * @param {string} type 
     * @param {number} duration 
     * @param {number|0} waitTime 
     * @param {boolean|true} canRemove 该效果是否可以被移除，默认true可以被移除
     */
    constructor(applicator, tactics, type, duration, waitTime, canRemove) {
        //来自谁
        this.applicator = applicator;
        //施加该效果的战法
        this.tactics = tactics;
        //状态类型
        this.type = type;
        //持续时间
        this.duration = duration;
        //是否是增益
        this.isBenefit = benefitStatus.includes(type);
        //等待时间
        this.waitTime = waitTime == undefined ? 0 : waitTime;
        //是否可被移除
        this.canRemove = canRemove == undefined ? true : canRemove;
        //被执行人
        this.beExecuted = null;
        //target是被施加该块的对象
    }
    /**
     * 初始化
     * @param {object} target 
     * @param {boolean|true} needReport 
     * @param {number|3} reportLevel 
     * @param {number|1} reportStyle 可选1，2：1类 A ....  2类：A【x】令B ....
     * @returns 
     */
    Init(target, needReport = true, reportLevel = 3, reportStyle = 1) {
        this.beExecuted = target;
        if (IsGetStatus(target, `免疫${this.type}`)) {
            let report = new Report();
            report.InitExecuteReport(3, target, `免疫${this.type}`);
            return false;
        }
        //检查抵御效果(指挥战法阶段不进行抵御)
        if (target.driver.turn >= 1) {
            const resistWork = target.STATUS_RESIST.some(block => {
                if (block.resistType == this.type) {
                    let rand = Math.floor(Math.random() * 100);
                    let realOdd = GetMoraleRate(target, block.rate)
                    if (rand < realOdd) {
                        let r = new Report();
                        r.InitTwoTargetReport(3, block.applicator, target, `【${block.tactics.name}】令`, `抵御了${this.type}状态`);
                        return true;
                    }
                    else {
                        let r = new Report();
                        r.InitExecuteReport(3, block.applicator, `【${block.tactics.name}】的抵御效果没有生效。生效几率为${realOdd}%`);
                    }
                }
            })
            if(resistWork) return false;
        }
        //执行即将陷入特殊状态时执行块
        target.BEFORE_FALL_INTO_STATUS.forEach(block => {
            block.GetData(this.applicator, this.tactics, this.type, this.duration, this.isBenefit, this.waitTime, this);
            block.Execute();
        })
        //检查冲突
        const abort = target.STATUS.some(block => {
            //找到同类型效果，冲突（只考虑类型，不考虑战法类型）
            if (block.type == this.type && this.type != '无法普通攻击' && this.type != '无法施法') {
                let report = new Report();
                report.InitExecuteReport(3, target, `已存在${block.type}效果`);
                return true;
            }
        })
        if (abort) return false
        target.STATUS.push(this);
        if (needReport) {
            this.InitReport(reportStyle, reportLevel)
        }
        //执行特殊状态时执行块
        target.FALL_INTO_STATUS.forEach(block => {
            block.GetData(this.applicator, this.tactics, this.type, this.duration, this.isBenefit, this.waitTime, this);
            block.Execute();
        })
        return true;
    }
    /**
     * 战报
     * @param {*} reportStyle 1类 A武将...  2类：A武将【x】令B武将 ... 3类: A武将【x】施加的...消失了 4类: A武将【x】施加的...被移除了 5类: A武将【x】移除了B武将 ...状态    
     * @param {*} reportLevel 
     */
    InitReport(reportStyle = 1, reportLevel = 3,newApplicator = null, newTact = null) {
        let msg = ``;
        if(this.type == '怯战' || this.type == '犹豫' || this.type == '混乱' || this.type == '暴走'){
            if(this.beExecuted.driver.turn > 0 && this.waitTime <= 0) msg = `陷入${this.type}中`;
            else msg = `${this.type}`
        }
        else if (this.type == "同心") msg = `进入同心状态`;
        else if (this.type == "围困") msg = `不可回复兵力`;
        else if (this.type == "先手" || this.type == "洞察") msg = `获得${this.type}`;
        else if (this.type == '挑衅' || this.type == '援护') msg = `受到${this.type}`
        else if (this.type == '无视兵种') msg = `无视兵种相克`
        else if (this.type == '不受指挥战法影响' || this.type == '不受敌方指挥战法影响') msg = `${this.type}`;
        else msg = `${this.type}`;

        if (this.waitTime > 0 || this.beExecuted.driver.turn < 1) msg += "(预备)"
        if(reportStyle == 1 && this.beExecuted.driver.turn < 1) msg += "效果已施加"
        let r = new Report();
        if (reportStyle == 1) r.InitExecuteReport(reportLevel, this.beExecuted, msg);
        if (reportStyle == 2) r.InitTwoTargetReport(reportLevel, this.applicator, this.beExecuted, `【${this.tactics.name}】令`, msg);
        if (reportStyle == 3){
            let r = new Report("状态信息");
            r.InitExecuteReport(reportLevel, this.beExecuted, `【${this.tactics.name}】施加的${this.type}状态消失了`);
        }
        if (reportStyle == 4) r.InitExecuteReport(reportLevel, this.beExecuted, `【${this.tactics.name}】施加的${this.type}状态被移除了`);
        if (reportStyle == 5) r.InitTwoTargetReport(reportLevel, newApplicator, this.beExecuted, `【${newTact.name}】移除了`, `${this.type}状态`);
    }
    SelfDestroy() {
        let remain = []
        for (let i in this.beExecuted.STATUS) {
            if (this.beExecuted.STATUS[i] != this) remain.push(this.beExecuted.STATUS[i]);
        }
        this.beExecuted.STATUS = remain;
    }
}


export class DamageStatusBlock {
    /**
     * 伤害型状态
     * @param {*} applicator 
     * @param {*} tactics 
     * @param {string} type 状态类型：恐慌，妖术，火攻，燃烧，动摇
     * @param {number} dmgMult 伤害率
     * @param {number} damageValue 计算好的伤害
     * @param {string} damageType 伤害基础类型：攻击或谋略
     * @param {*} duration 
     * @param {*} waitTime 
     * @param {boolean|true} isExecute 是否执行效果还是单纯挂状态
     * @param {boolean|true} canRemove 该效果是否可以被移除，默认true可以被移除
     * @param {Function|null} callBack 如果有需要的话可以传入额外的执行函数(调用时机是：当持续效果被触发后执行)
     */
    constructor(dmgId, applicator, tactics, type, dmgMult, damageValue, damageType, duration, waitTime, isExecute, canRemove, callBack) {
        this.dmgId = dmgId;
        this.applicator = applicator;
        this.tactics = tactics;
        this.type = type;
        this.dmgMult = dmgMult;
        this.damageValue = damageValue
        this.damageType = damageType;
        this.duration = duration;
        this.waitTime = waitTime == undefined ? 0 : waitTime;
        this.isExecute = isExecute == undefined ? true : isExecute;

        this.beExecuted = null;

        this.canRemove = canRemove == undefined ? true : canRemove;
        this.callBack = callBack == undefined ? null : callBack;
    }
    /**
     * 
     * @param {*} target 
     * @param {*} needReport 
     * @param {*} reportLevel 
     * @param {number} reportStyle 1表示单目标施加战报，2表示2目标某战法施加战报
     * @returns 
     */
    Init(target, needReport = true, reportLevel = 3, reportStyle = 1) {
        this.beExecuted = target;
        //冲突判定
        let refresh = false;
        let remain = [];
        const abort = target.SPECIAL_STATUS.some(block => {
            if (block.tactics.type == this.tactics.type && block.type == this.type) {
                if (block.damageValue >= this.damageValue) {
                    let r = new Report();
                    let text = ""
                    if (block.damageValue > this.damageValue) text = `已存在更强的${this.type}效果`
                    else text = `已存在相同的${this.type}效果`
                    r.InitExecuteReport(reportLevel, target, text);
                    return true;
                }
                else {
                    let r = new Report();
                    let text = `${this.type}效果刷新了`;
                    r.InitExecuteReport(reportLevel, target, text);
                    refresh = true;
                }
            }
            else remain.push(block);
        });
        if (abort) return false;
        target.SPECIAL_STATUS = remain;
        target.SPECIAL_STATUS.push(this);
        if (needReport && !refresh) {
            this.InitReport(reportStyle,reportLevel);
        }
        //执行陷入持续伤害块
        target.FALL_INTO_CONTINUE_DAMAGE.forEach(block => {
            block.GetData(this.type, this.damageType, this.damageValue, this.applicator, this.tactics, this.duration, this);
            block.Execute();
        });
        return true;
    }
    /**
     * 生成战报
     * @param {*} reportStyle 
     * @param {*} reportLevel 1: A武将x效果已施加 2:A武将【x】对B武将施加了x效果 3: A武将【x】施加的x效果消失了 4: A武将【x】施加的x效果被移除了 5: A武将【x】移除了B武将x效果
     * @param {*} newApplicator 
     * @param {*} newTact 
     */
    InitReport(reportStyle = 1, reportLevel = 3,newApplicator = null, newTact = null){
        let r = new Report();
        let msg = `${this.type}`;
        if(this.waitTime > 0) msg += "(预备)";
        msg += "效果已施加";
        if(reportStyle == 1) r.InitExecuteReport(reportLevel,this.beExecuted,msg);
        if(reportStyle == 2) r.InitTwoTargetReport(reportLevel,this.applicator,this.beExecuted,`【${this.tactics.name}】对`,`施加了${this.type}效果`);
        if(reportStyle == 3){
            let r = new Report("状态信息");
            r.InitExecuteReport(reportLevel,this.beExecuted,`【${this.tactics.name}】施加的${this.type}效果消失了`);
        }
        if(reportStyle == 4) r.InitExecuteReport(reportLevel,this.beExecuted,`【${this.tactics.name}】施加的${this.type}效果被移除了`);
        if(reportStyle == 5) r.InitTwoTargetReport(reportLevel,newApplicator,this.beExecuted,`【${newTact.name}】移除了`,`的${this.type}效果`);
    }
    //状态伤害引发
    Execute(target, isByTrigger = false) {
        let rpLevel = 2;
        if (isByTrigger) rpLevel = 3
        Hurt(this.dmgId, this.applicator, target, this.damageValue, false, this.damageType, this.tactics, true, 3, this.type, rpLevel);
        if (this.callBack != null) {
            this.callBack();
        }
    }
}


export class CureStatusBlock {
    /**
     * 治疗状态（休整和急救）
     * @param {*} applicator 
     * @param {*} tactics 
     * @param {*} type 
     * @param {*} cureMult 
     * @param {*} cureValue 
     * @param {*} duration 
     * @param {*} waitTime 
     * @param {*} rate 触发几率
     * @param {*} isExecute 
     * @param {boolean|true} canRemove 该效果是否可以被移除，默认true可以被移除
     */
    constructor(applicator, tactics, type, cureMult, cureValue, duration, waitTime, rate, isExecute, canRemove) {
        //来自谁
        this.applicator = applicator;
        //施加该效果的战法
        this.tactics = tactics;
        //类型,急救或者是休整
        this.type = type;
        //治疗倍率
        this.cureMult = cureMult;
        //计算的治疗量
        this.cureValue = cureValue;
        //持续时间
        this.duration = duration;
        //准备时间
        this.waitTime = waitTime;
        //触发几率
        this.rate = rate;
        //是否执行
        this.isExecute = isExecute == undefined ? true : isExecute;
        this.canRemove = canRemove == undefined ? true : canRemove;
        this.beExecuted = null;
    }
    Init(target, needReport = true, reportLevel = 3, reportStyle = 1) {
        this.beExecuted = target;
        let refresh = false;
        let remain = [];
        //检查冲突
        for (let i in target.CURE_STATUS) {
            let block = target.CURE_STATUS[i];
            if (block.type == this.type && block.tactics.type == this.tactics.type) {
                if (block.cureMult >= this.cureMult) {
                    let r = new Report();
                    let text = block.cureMult > this.cureMult ? `已存在更强的${block.type}效果` : `已存在相同的${block.type}效果`;
                    r.InitExecuteReport(3, target, text);
                    return false;
                }
                else if(this.cureMult > block.cureMult){
                    let r = new Report();
                    r.InitExecuteReport(3, target, `${block.type}效果刷新了`);
                    refresh = true;
                }
                else remain.push(block);
            }
        }
        target.CURE_STATUS = remain;
        target.CURE_STATUS.push(this);
        //战报输出
        if (needReport && !refresh) {
            this.InitReport(reportStyle,reportLevel);
        }
        return true;
    }
    /**
     * 生成战报
     * @param {*} reportStyle 1: A武将x效果已施加 2:A武将【x】对B武将施加了x效果 3: A武将【x】施加的x效果消失了 4: A武将【x】施加的x效果被移除了 5: A武将【x】移除了B武将x效果
     * @param {*} reportLevel 
     * @param {*} newApplicator 
     * @param {*} newTact 
     */
    InitReport(reportStyle = 1, reportLevel = 3,newApplicator = null, newTact = null){
        let r = new Report();
        let msg = `${this.type}`;
        if(this.waitTime > 0) msg += "(预备)";
        msg += "效果已施加";
        if(reportStyle == 1) r.InitExecuteReport(reportLevel,this.beExecuted,msg);
        if(reportStyle == 2) r.InitTwoTargetReport(reportLevel,this.applicator,this.beExecuted,`【${this.tactics.name}】对`,`施加了${this.type}效果`);
        if(reportStyle == 3){
            let r = new Report("状态信息");
            r.InitExecuteReport(reportLevel,this.beExecuted,`【${this.tactics.name}】施加的${this.type}效果消失了`);
        }
        if(reportStyle == 4) r.InitExecuteReport(reportLevel,this.beExecuted,`【${this.tactics.name}】施加的${this.type}效果被移除了`);
        if(reportStyle == 5) r.InitTwoTargetReport(reportLevel,newApplicator,this.beExecuted,`【${newTact.name}】移除了`,`的${this.type}效果`);
    }
    Execute(target, isByTrigger = false) {
        let realOdd = GetMoraleRate(target, this.rate);
        let rand = Math.floor(Math.random() * 100);
        if (this.rate >= 100 || rand < realOdd) {
            let reportL = 2;
            if (isByTrigger) reportL = 3;
            Cure(this.applicator, target, this.cureValue, this.tactics, true, null, 3, reportL, this.type);
        }
        else {
            let r = new Report();
            r.InitTwoTargetReport(2, this.applicator, target, `【${this.tactics.name}】的${this.type}效果对`, `没有生效，生效几率为${realOdd}%`);
        }

    }
}

