import Report from "../js/report";

import { GetRange } from "../func/get";
class RangeBlock{
    constructor(applicator,tactics,isCommonAttack,value,duration,waitTime,workTactics){
        //施加该块的武将
        this.applicator = applicator;
        //施加该战法的类型
        this.tactics = tactics;
        //是普通攻击增加或减少距离,否则就是战法增加或减少距离
        this.isCommonAttack = isCommonAttack;
        //数值
        this.value = value;
        //持续时间
        this.duration = duration
        //等待时间
        this.waitTime = waitTime;
        //是否增益
        this.isBenefit = value > 0 ? true : false;
        //是否作用在某个具体战法
        this.workTactics = workTactics || null;
        this.beExecuted = null;
    }
    Init(target,needReport = true,reportLevel = 3,isRepeat = false,reportStyle = 1){
        this.beExecuted = target;
        //冲突检测
        if(!isRepeat){
            for(let i in target.RANGE){
                if(target.RANGE[i].tactics.type == this.tactics.type
                &&target.RANGE[i].isCommonAttack == this.isCommonAttack){
                    let text = "";
                    if(this.isCommonAttack) text = "已存在攻击距离";
                    else text = "已存在战法距离";
                    let report = new Report();
                    if(this.value > 0 && target.RANGE[i].value > 0){
                        text += "提升效果";
                        report.InitExecuteReport(3,target,text);
                        return false;
                    }
                    else if(this.value < 0 && target.RANGE[i].value < 0){
                        text += "降低效果";
                        report.InitExecuteReport(3,target,text);
                        return false;
                    }
                }
            }
        }
        
        target.RANGE.push(this);
        if(needReport){
            this.InitReport(reportStyle,reportLevel);
        }
        return true;
    }
    /**
     * 生成战报
     * @param {*} reportStyle 1: A武将...距离提高x 2: A武将【x】令B武将...距离提高x 3: A武将...距离提高x效果消失了 4: A武将...距离提高x效果被移除了 5: A武将【x】移除了B武将...距离提高x效果
     * @param {*} reportLevel 
     * @param {*} newApplicator 
     * @param {*} newTact 
     */
    InitReport(reportStyle = 1, reportLevel = 3, newApplicator = null, newTact = null){
        let r = new Report();
        let msg = "";
        msg += this.isCommonAttack ? "攻击距离" : "战法距离";
        msg += this.isBenefit ? "提高" : "降低";
        msg += this.isCommonAttack ? `${Math.abs(this.value)}(${GetRange(this.beExecuted)})` : `提高${Math.abs(this.value)}` ;
        if(reportStyle == 1) r.InitExecuteReport(reportLevel,this.beExecuted,msg);
        if(reportStyle == 2) r.InitTwoTargetReport(reportLevel,this.applicator,this.beExecuted,`【${this.tactics.name}】令`,msg);
        if(reportStyle == 3){
            r = new Report("状态信息");
            r.InitExecuteReport(reportLevel,this.beExecuted,msg + "效果消失了");
        }
        if(reportStyle == 4) r.InitExecuteReport(reportLevel,this.beExecuted,msg + "效果被移除了");
        if(reportStyle == 5 && newApplicator && newTact) r.InitTwoTargetReport(reportLevel,newApplicator,this.beExecuted,`【${newTact.name}】移除了`,msg+"效果");
    }
}

export default RangeBlock;