import Report from "../js/report";

import { GetAttr } from "../func/get";

export class AttrBlock {
    /**
     * 属性块
     * @param {object} applicator 施加该块的武将
     * @param {object} tactics 施加该战法
     * @param {string} attrType 该块的作用的属性类型
     * @param {number} value 数值
     * @param {number} [duration=99] 持续时间
     * @param {boolean} isRepeat 是否可以叠加，默认不可叠,即相同战法施加的属性效果不会产生冲突
     * @param {boolean} isBenefit 是否增益
     */
    constructor(applicator, tactics, attrType, value, duration, isRepeat, isBenefit) {
        this.applicator = applicator;
        this.tactics = tactics;
        this.attrType = attrType;
        this.value = value;
        this.duration = duration;
        this.isBenefit = isBenefit == undefined ? (this.value >= 0 ? true : false) : isBenefit;
        this.isRepeat = isRepeat == undefined ? false : isRepeat;
        //被施加该效果的武将
        this.owner = null;
    }
    /**
     * 
     * @param {object} target 
     * @param {boolean} needReport 
     * @param {number} reportLevel 
     * @param {number} reportStyle 1类：A武将x属性提高x(x) 2类：A武将【x】令B武将属性提高x(x) 3类: A武将x属性提高x(x)效果消失了 4类: A武将x属性提高x(x)效果被移除了 5类: A武将【x】移除了B武将属性提高x(x)效果
     * @returns 
     */
    Init(target, needReport = true, reportLevel = 3,reportStyle=1) {
        //needReport是否需要自动生成战
        this.owner = target;
        //是否叠加
        let work = true;
        if (this.isRepeat) {
            target.ATTR.forEach(block=>{
                if (block.tactics == this.tactics && block.attrType == this.attrType) {
                    block.value += this.value;
                    block.duration = this.duration;//持续时间刷新
                    if (needReport) {
                        let report = new Report();
                        let msg = `${this.attrType}属性`;
                        if (block.value > 0) {
                            msg += `提高${block.value}(${GetAttr(target, this.attrType)})`;
                        }
                        else msg += `降低${Math.abs(block.value)}(${GetAttr(target, this.attrType)})`;
                        report.InitExecuteReport(reportLevel, target, msg);
                    }
                    work = false;
                }
            })
        }
        let abort = false;
        if (work) {
            //判断冲突
            let remain = [];
            abort = target.ATTR.some((block)=>{
                //添加的属性值相同
                if (block.attrType == this.attrType && this.tactics.type == block.tactics.type) {
                    //加属性
                    if (this.isBenefit && block.isBenefit) {
                        //更强不替换
                        if (this.value <= block.value) {
                            let report = new Report();
                            let str = `已存在更强的${block.attrType}属性提升效果`;
                            report.InitExecuteReport(reportLevel, target, str);
                            return true;
                        }
                        else {
                            let r = new Report();
                            r.InitExecuteReport(reportLevel, target, `${block.attrType}属性提升效果刷新了`)
                        }
                    }
                    //减属性
                    else if (!this.isBenefit && !block.isBenefit) {
                        //更强不替换
                        if (this.value >= block.value) {
                            let report = new Report();
                            let str = `已存在更强的${block.attrType}属性降低效果`;
                            report.InitExecuteReport(reportLevel, target, str);
                            return true;
                        }
                        else {
                            let r = new Report();
                            r.InitExecuteReport(reportLevel, target, `${block.attrType}属性降低效果刷新了`)
                        }
                    }
                    else remain.push(block);
                }
                else remain.push(block);
            })
            if(abort) return false;
            target.ATTR = remain;
            
            
            //属性被施加前执行
            target.BEFORE_APPLIED_ATTR_EXECUTE.forEach(block=>{
                if (block.duration > 0) {
                    if (block.isAppliedAd && this.attrType == '攻击'
                        || block.isAppliedTact && this.attrType == '谋略'
                        || block.isAppliedDe && this.attrType == '防御'
                        || block.isAppliedSpeed && this.attrType == '速度'
                    ) {
                        block.Execute(this);
                    }
                }
            })
            target.ATTR.push(this);
        }
        if (needReport && work) {
            this.InitReport(reportStyle,reportLevel)
        }
        //属性被施加后执行
        target.AFTER_APPLIED_ATTR_EXECUTE.forEach(block => {
            if (block.duration > 0) {
                if (block.isAppliedAd && this.attrType == '攻击'
                    || block.isAppliedTact && this.attrType == '谋略'
                    || block.isAppliedDe && this.attrType == '防御'
                    || block.isAppliedSpeed && this.attrType == '速度'
                ) {
                    block.Execute(this);
                }
            }
        });
        return true;
    }
    /**
     * 生成战报
     * @param {*} reportStyle 1类：A武将x属性提高x(x) 2类：A武将【x】令B武将属性提高x(x) 3类: A武将x属性提高x(x)效果消失了 4类: A武将x属性提高x(x)效果被移除了 5类: A武将【x】移除了B武将属性提高x(x)效果
     * @param {*} reportLevel 
     */
    InitReport(reportStyle=1,reportLevel=3,newApplicator = null, newTact = null){
        let report = new Report();
        let msg = `${this.attrType}属性`;
        msg += this.isBenefit ? "提高" : "降低";
        //在战法准备阶段且数值为0，则不输出具体0数值，改为显示效果已施加
        if(this.owner.driver.turn < 1 && this.value == 0){
            msg += "效果已经施加"
        }
        else msg += `${Math.abs(this.value)}(${GetAttr(this.owner, this.attrType)})`;
        
        if(reportStyle == 1 || reportStyle == null) report.InitExecuteReport(reportLevel, this.owner, msg);
        if(reportStyle == 2) report.InitTwoTargetReport(reportLevel,this.applicator,this.owner,`【${this.tactics.name}】令`,msg);
        if(reportStyle == 3){
            let report = new Report("状态信息");
            report.InitExecuteReport(reportLevel, this.owner, msg + "效果消失了");
        }
        if(reportStyle == 4) report.InitExecuteReport(reportLevel, this.owner, msg + "效果被移除了");
        if(reportStyle == 5) report.InitTwoTargetReport(reportLevel,newApplicator,this.owner,`【${newTact.name}】移除了`,msg+"效果");
    }
}

export class AttrAppliedExecuteBlock{
    constructor(applicator,tactics,isBeforeApplied,isAppliedAd,isAppliedTact,isAppliedDe,isAppliedSpeed,execute,duration){
        this.applicator = applicator;
        this.tactics = tactics;
        this.isBeforeApplied = isBeforeApplied || true;
        this.isAfterApplied = !this.isBeforeApplied
        this.isAppliedAd = isAppliedAd || true;
        this.isAppliedTact = isAppliedTact || true;
        this.isAppliedDe = isAppliedDe || true;
        this.isAppliedSpeed = isAppliedSpeed || true;
        this.execute = execute;
        this.duration = duration;
        this.beExecuted = null;
        this.attrBlock = null;
    }
    Init(target){
        this.beExecuted = target
        if(this.isBeforeApplied) target.BEFORE_APPLIED_ATTR_EXECUTE.push(this);
        else target.AFTER_APPLIED_ATTR_EXECUTE.push(this);
    }
    Execute(attrBlock){
        this.attrBlock = attrBlock;
        this.execute();
    }
}