export class LaunchComboTacticsBlock{
    constructor(applicator,tactics,executeFunc,duration){
        this.applicator = applicator;
        this.tactics = tactics;
        this.executeFunc = executeFunc;
        this.duration = duration;

        this.beExecuted = null;

        this.attacker = null;
        this.comboTarget = null;//追击目标
        this.comboTactics = null;

    }
    GetData(attacker,comboTarget,comboTactics){
        this.attacker = attacker;
        this.comboTarget = comboTarget;
        this.comboTactics = comboTactics;
    }
    Init(target){
        this.beExecuted = target;
        target.LAUNCH_COMBO_EXECUTE.push(this);
    }
    Execute(){
        this.executeFunc();
    }
    SelfDestroy() {
        let remain = []
        this.beExecuted.LAUNCH_COMBO_EXECUTE.forEach(block => {
            if(block != this) remain.push(block);
        });
        this.beExecuted.LAUNCH_COMBO_EXECUTE = remain;
    }
}